<template>
  <hr>
  <h1>Вопрос:
    Понятие «леверидж».
    Сущность левериджа в финансовом менеджменте.
    Эффект финансового левериджа при управлении структурой капитала организации.
  </h1>
  <hr>
  <h3>
    <b>Левередж (от англ. lever – рычаг) </b>  — это стратегия использования долга (заемного капитала) для увеличения прибыли от инвестиций. Благодаря левереджу потенциальная прибыль от
    инвестиции увеличивается в несколько раз. В то же время левередж также увеличивает потери в несколько раз, если инвестиции себя не оправдают. Если у компании, объекта
    недвижимости или инвестиции «высокий левередж», это означает, что у объекта больше долгов, чем собственного капитала.<br>
    <br>
<!--    ЧАСТЬ 2 -->
    <hr>
    Сущность финансового левериджа заключается в том, что собственный капитал заменяется заемными деньгами, соответственно все финансовые операции осуществляются с использованием
    только денежных средств, взятых в кредит. С ростом доходов организации финансовый леверидж повышает риски, так как процент по кредиту нужно платить, независимо от того,
    получена прибыль от вложений или нет.<br>
    <br>
    Финансовый леверидж определяет уровень воздействия структуры капитала на величину прибыли организации, а разные варианты добавления издержек по кредиту в себестоимость влияет
    на уровень чистой прибыли и чистую рентабельность собственных финансовых средств.<br>
    Различают три вида финансового левериджа по целесообразности использования:<br>
    - положительный (выгода от привлечения выше процентов за пользование кредитом); <br>
    - отрицательный (приобретенные за счет заемных средств активы не окупаются, прибыль отсутствует или ниже перечисленных процентов); <br>
    - нейтральный (затраты от полученных заемных средств равны доходам от вложений). <br>
    Финансовый леверидж влияет на инвестиционный рейтинг компании, возможность его развития и повышение прибыли. Соответственно в ходе планирования деятельности субъекта
    хозяйствования следует учитывать этот показатель.<br>
<!--    ЧАСТЬ 3 -->
    <hr>
    Одна из главных задач формирования капитала - оптимизация его структуры с учетом заданного уровня его доходности и риска - реализуется разными методами.
    Одним из основных механизмов реализации этой задачи является финансовый леверидж (другими словами "операционный рычаг").<br>
    Финансовый леверидж характеризует использование предприятием заемных средств, которое влияет на изменение коэффициента рентабельности собственного капитала.
    Иными словами, финансовый леверидж представляет собой объективный фактор, возникающий с появлением заемных средств в объеме используемого предприятием капитала,
    позволяющий ему получить дополнительную прибыль на собственный капитал.<br><br>

    Существует два взаимосвязанных показателя, которые надо различать — плечо или коэффициент финансового рычага (<b>КФР</b>) и эффект финансового рычага (<b>ЭФР</b>).
    Сначала необходимо рассчитать КФР по простой формуле:<br>

    <b>КФР = Заемный капитал / Собственный капитал</b><br>

    Значение этого показателя всегда положительное и должно выражаться в денежных единицах.
    КФР легко посчитать по статьям бухгалтерского баланса организации: в числитель необходимо поставить итог по строкам
    «Заемные средства» разделов «Долгосрочные обязательства» и «Краткосрочные обязательства», а в знаменатель — значение строки «Капитал и резервы».<br>

    Если итог выйдет более единицы — это говорит о наличии рисков. Общее правило такое: чем выше показатель КФР, тем плачевнее финансовые дела у компании.
    Иначе говоря, если доля кредитных денег составляет большую часть капитала, то компания может не потянуть имеющуюся долговую нагрузку и обанкротиться.<br>

    При этом стоит учитывать, что нормативное значение КФР зависит от отраслевых особенностей. К примеру, для такой капиталоемкой деятельности,
    как машиностроение, КФР будет гораздо выше, чем для деятельности в сфере информационных технологий. Иными словами  — справедливо сравнивать этот показатель лишь в
    границах одной отрасли.<br>

    Показатель, отражающий уровень дополнительно генерируемой прибыли на собственный капитал при различной доле использования заемных средств, называется
    <b>эффектом финансового левериджа.</b> Он рассчитывается по следующей формуле:<br>
    <b>ЭФР = ( 1  — Налоговый корректор) * Дифференциал (DFL) * КФР</b><br>
    <b>Налоговый корректор.</b> Для его расчета необходимо вычесть из единицы применяемую ставку по налогу на прибыль. Он показывает изменение  уровня финансового рычага
    при усилении или ослаблении налоговой нагрузки. Для российских компаний действует ставка 20%, в таком случае налоговый корректор будет равен величине 0,8 ( 1  — 0,2 ).<br>
    <b>Дифференциал финансового рычага (DFL).</b> Представляет собой разницу между рентабельностью активов (ROA) и кредитной ставкой. Процентный показатель ROA является отношением
    чистой прибыли к активам компании. Он отражает объем доходности, которая получена от использования основных фондов. Отрицательное значение при расчете DFL возникает, если ROA
    ниже стоимости заемного капитала. Это свидетельствует о высоких рисках возникновения убытков в недалеком будущем.<br>
    В результате, если ЭФР получился со знаком «+» — достигается положительный эффект финансового рычага. Это означает, что привлечение заемных средств будет рациональным решением,
    ведь это может повысить рентабельность и экономическую силу собственных средств. ЭФР со знаком «—» может получиться только при отрицательном DFL, то есть когда рентабельность
    капитала ниже процентной ставки. Такое значение ЭФР характеризует деятельность организации как убыточную или находящуюся на грани возникновения финансовых трудностей.

  </h3>
