  <h1>Вопрос:
    Организация финансового менеджмента на предприятии (организации).
    Принципы организации финансового менеджмента на предприятии.
    Цели и задачи организации финансового менеджмента на предприятии</h1>
  <hr>
  <h3>
    Финансовый менеджмент на предприятии - это совокупность служб и структурных подразделений, которые разрабатывают управленческие решения в отдельных аспектах финансовой деятельности.
<p></p>
    Финансовый менеджмент в копании занимает отдельное место и связан с управлением финансовыми отношениями. Данный вид деятельности позволяет согласовывать и формировать оптимальное решение по финансовому обеспечению стратегических и оперативных целей.
    <p></p>
    Функциональный подход в компании сопровождает отдельный блок, который связан с осуществлением финансовой деятельности. Задача руководителя финансовой службы заключается в согласовании работы в структурных подразделениях, в частности, выполнении функциональных обязанностей.
    <br>
    Структурные подразделения, связанные с финансовой деятельностью, делятся на: <br>
    - финансовые отделы<br>
    - планово-экономические отделы<br>
    - бухгалтерию<br>
    <b>В обязанности финансового отдела</b> входит планирование, контроль расхода денежных средств, осуществление расчетов между кредиторами и дебиторами, планирование и мониторинг финансов. Финансовый отдел формирует аналитическую информацию для внешних и внутренних пользователей, проводит финансово-экономический анализ и разрабатывает рекомендации по улучшению финансового состояния. На плечи финансового отдела ложится проведение тендеров и сделок, их оценка по показателям прибыли и рентабельности.
    <br>
    <b>Планово-экономический отдел</b> осуществляет экономическое и финансовое планирование в организации. Основная работа данного отдела заключается в разработке бюджета и улучшении экономических показателей, например, повышении нормативов работы подразделений, увеличении прибыли, росте компании. Планово-экономический отдел отвечает за оптово-розничные цены и разрабатывает мероприятия, которые позволяют эффективно использовать материальные, трудовые и финансовые ресурсы. Отдел проводит комплексный экономический анализ компании и предоставляет отчеты для руководящего состава компании.
<br>
    <b>Бухгалтерия</b> выполняет учетную деятельность в компании, формирует отчётную калькуляцию себестоимости продукции и составляет бухгалтерскую отчетность. Бухгалтерия проводит инвентаризацию финансов и товарно-материальных ценностей. Основной задачей этого отдела является соблюдение финансовой дисциплины и контроль за взысканиями дебиторской и кредиторской задолженности. Помимо этого, бухгалтерия отвечает за отчетную деятельность в компании. Бухгалтерия может приносить убытки, в случае, если отчетность не оптимизирована или составлена некорректно.
<hr>
    К основным принципам финансового менеджмента можно отнести:
    <br>
    <b>1. Ориентированность на стратегические цели развития организации.</b> Управленческие решения в области финансовой деятельности в текущем периоде не должны противоречить стратегическим целям организации.
    <br><b>2. Адаптивность.</b> Финансовой менеджмент призван учитывать изменения факторов внешней и внутренней среды, конъюнктуры финансового рынка, ресурсного потенциала, форм организации производственной и финансовой деятельности, финансового состояния и других параметров функционирования организации.
    <br><b>3. Комплексность.</b> Финансовый менеджмент как комплексная управляющая система связывает все управленческие решения в области формирования, распределения и использования финансовых ресурсов организации.
    <br><b>4. Интегрированность.</b> Любое управленческое решение прямо или косвенно оказывает влияние на формирование денежных потоков и результаты финансовой деятельности, поэтому финансовый менеджмент непосредственно связан с общей системой управления организацией.
<hr>
    <br>
    <b>Цель финансового менеджмента</b> - является выработка определенных решений для достижения оптимальных конечных результатов и нахождения оптимального соотношения между краткосрочными и долгосрочными целями развития предприятия и принимаемыми решениями в текущем и перспективном финансовом управлении.
    <br><b>Главной целью финансового менеджмента</b> является обеспечение роста благосостояния собственников предприятия в текущем и перспективном периоде,
   обеспечение сбалансированности поступления денежных средств (платежеспособности и ликвидности предприятия), обеспечение достаточного уровня рентабельности и продаж за счет
    гибкой ценовой политики и снижения издержек.
<br><b>Задачи:</b>
    <br><b>1.Управление капиталом (пассивами)</b> – определение потребности в капитале для финансирования формируемых активов организации; оптимизация структуры капитала в целях обеспечения наиболее эффективного его использования.
    <br><b>2. Управление оборотными и внеоборотными активами</b> – формирование необходимого объема ресурсов организации, полученных за счет инвестированного в них капитала, способных обеспечить получение устойчивого дохода и рост рыночной стоимости организации, с учетом фактора времени и ликвидности.
      <br><b>3. Управление инвестициями</b> – определение параметров инвестиционной деятельности организации; оценка инвестиционной привлекательности финансовых инструментов; выбор наиболее эффективных инвестиционных решений; определение источников финансирования инвестиций; формирование оптимального инвестиционного портфеля организации; мониторинг инвестиционного портфеля; поддержание оптимального уровня рисков и доходности по портфелю.
        <br><b>4. Управление денежными потоками</b> – синхронизация и обеспечение сбалансированности притоков и оттоков денежных средств организации, оптимизация использования денежных ресурсов для обеспечения ликвидности и финансовой устойчивости организации.
          <br><b>5. Управление финансовыми рисками</b> – выявление, классификация и оценка финансовых рисков; определение величины допустимого уровня финансового рисков, принимаемых организацией; определение наиболее эффективных способов, методов и процедур минимизации рисков; разработка и реализация мероприятий по устранению и минимизации рисков.

