  <h3>
    1. экономическая роль ссудного процента заключается для конкретных кредиторов в получении прибыли или доходов за его работу, а в общем для страны, государства он выступает в качестве мощнейшего
    средства регулирования экономики. Т. е. роль регулирования – это основная роль ссудного процента. Учитывая, что кредит выступает в роли регулятора экономики, безусловно, ему присуща и
    контрольная функция. Это заключается в принципах обеспеченности кредита, возвратности, срочности, платности, целевого использования кредитных ресурсов.<br><br>

    2. Устанавливаемая Центральным банком ставка платы за ресурсы (ставка рефинансирования) наряду с нормой обязательных резервов и условиями выпуска и обращения государственных ценных бумаг
    является эффективным средством управления коммерческими банками. Центральные банки определяют единство процентной политики в масштабах хозяйства, стимулируя повышение или понижение
    процентных ставок.<br><br>

    3. Посредством процента осуществляется регулирование объема привлекаемых банком депозитов. Рост потребности экономики в кредитах должен быть покрыт соответствующим приростом банковских депозитов
    как источников кредитования. Это ведет к повышению ставок депозитного процента до размера, уравновешивающего предложение депозитов и спрос на них со стороны кредитной организации. Наоборот,
    при сокращении потребности экономики в кредитах будут снижаться доходы банка от предоставляемых ссуд.<br>


