    <b>Распределительная функция: </b>связана с распределением ни всей стоимости, а только вновь созданной; передача части вновь созданной стоимости сопровождается сменой собственника.
    II .пата носит обязательный характер<br>

    <b>Стимулирующая</b> функция: ссудный процент стимулирует не только рациональное использование кредитных ресурсов, но и рациональное управление активами и пассивами бкнка в условиях угрозы
    процентного риска<br>

    <b>Функция сохранения ссудного капитала: </b>ссудный процент является своеобразной ценой (платой) кредитору за риск отчуждения его собственных временно свободных ресурсов. Ссудный процент
    выступает гарантией возврата не только суммы выделенной ссуды, но и сохранения стоимости<br>

    <b>Функция средства возможности соизмерения ценности настоящих и будущих благ.</b> Функция создаст возможность с помощью дисконтирования соизмерять ценность настоящих и будущих благ, и,
    следовательно, строить шкалу предпочтений во времени<br><br>

    <b>1. Распределительная функция. </b><br>
    Поскольку ссудный процент - это цена заемных денег, то, как и любая цена, он выполняет функцию распределения денежного и соответственно реального капитала среди различных предприятий,
    фирм и инвестиционных объектов. Согласно этой функции имеющиеся в наличии деньги распределяются между теми заемщиками, степень доходности или ожидаемая норма прибыли которых является
    достаточно высокой, следовательно, гарантируется выплата процентов. Процент, выполняя свою функцию, распределяет реальный капитал между теми отраслями, где он окажется наиболее
    производительным и, соответственно, наиболее прибыльным. Такое размещение финансовых ресурсов работает в интересах общества в целом.<br>

    Процент не выполняет идеально задачу распределения ссудного капитала в целях наиболее производительного применения, но перекладывая затраты, связанные с уплатой ссудного процента на потребителей,
    крупные промышленные субъекты в состоянии контролировать предложение и манипулировать своими ценами.<br>

    Большой размер и престиж крупных промышленных предприятий, холдингов также позволяет им получать ссуды на более выгодных условиях, пока на рынке денежного капитала не появятся менее известные
    компании, ожидаемые прибыли которых фактически могут быть выше.<br><br>

    <b>2. Стимулирующая функция. </b><br>
    Ссудный процент - не пассивный участник кредитной сделки, он оказывает стимулирующее воздействие на функционирование заемных средств, эффективное использование ссужаемой стоимости.
    Необходимость выплаты процента стимулирует заемщика к получению максимальной прибыли в условиях рыночной конкуренции, чтобы расплатится с кредитором и иметь средства для расширенного
    воспроизводства. Если предприятие не будет рационально применять заемные средства (а также и собственные), то проценты за пользование ссудой поглотят всю его прибыль.<br>

    В условиях рыночного формирования уровня ссудного процента привлечение в оборот заемных средств на платной основе является выгодным только при покрытии ссудами временных и необходимых
    дополнительных потребностей. Всякое излишнее использование ссуд снижает общий уровень рентабельности вложений.<br>

    Посредством процента стимулируется объем привлекаемых банком депозитов: необходимость увеличения банковских ресурсов (особенно депозитных) приведет к повышению депозитного процента до размера,
    уравновешивающего предложение депозитов и спрос на них со стороны банка. Реализуется эта функция на практике использованием дифференцированных процентных ставок, изменяющихся в зависимости от
    общих показателей эффективности кредитного объекта, снижением риска, доходов кредитования, характеристики клиентов.<br><br>

    <b>3. Функция сохранения ссудного капитала.</b><br>
    При изучении этой функции ссудного процента следует учесть, что сохранение стоимости средств, отданных во временное пользование происходит в том случае, если к кредитору от заемщика
    возвращаются суммы равноценной стоимости, предоставленным во временное пользование.<br>

    Большое значение эта функция процента имела в первые годы становления независимой экономики Украины и ее банковской системы. В период с 1992 по 1994 годы в связи с инфляцией, банки в
    процессе кредитования перекладывали риск обесценения украинской валюты (карбованца) на всех заемщиков. Это достигалось посредством взыскания высокой процентной ставки по ссудам - до
    600 процентов годовых, а также начислением сложных процентов по кредитным операциям.<br>

    Важное значение имеет эта функция и в настоящее время. При постоянном росте вкладов населения в банки, основным из факторов их дальнейшего роста является величина процента. Для того, чтобы
    выполнялось объективное требование этой функции процента, величина процентных ставок по вкладам не должна быть меньше темпов инфляции.<br>

    Ссудный процент, таким образом, являлся известной гарантией возврата не только суммы выданных ссуд, но и сохранения стоимости, предоставленной во временное пользование.<br><br>

    <b>4. Функция средства возможности соизмерения ценности настоящих и будущих благ.</b> <br>
    Важным фактором, влияющим на уровень процента, является взаимодействие спроса и предложения на денежном рынке.<br>

    Как и любая цена, процент становится результатом торга и устанавливается на уровне, обеспечивающим равновесие ссудного капитала.<br>

    Это происходит потому, что при высоких ставках всё больше экономических субъектов начинают предпочитать текущему потреблению потребление в будущем. Отсюда вытекает четвертая функция
    процента -создание возможности соизмерять ценность настоящих и будущих благ, и, следовательно, строить шкалу предпочтений во времени. Наличие дифференцированной шкалы процентных
    ставок и разнообразных форм вложений способствует развитию кредитной системы, так как у потребителей появляется широкий выбор размещения денежных средств.

