    При бартерном обмене происходит обмен Т на Т. У бартера есть множество недостатков. Должны совпадать интересы двух обменивающихся сторон. Совпадение интересов должно произойти как по 
    времени, так и по количеству и качеству товара, что при большом количестве товаров требует значительных усилий, времени и затрат.<br>
    Первоначально в положении денег оказывались товары, имевшие устойчивый спрос и широкое хождение именно в силу признаваемой всеми полезности (скот, меха, табак, рыба). След., первым видом
