    1) износостойкость, 2) портативность, 3) стабильность, 4) однородность, 5) делимость, 6) узнаваемость и др. В связи с тем, что драгметаллы соответствовали этим требованиям, они и 
    «взяли на себя» выполнение этой миссии.<br>
    К непосредственным предпосылкам появления денег относятся:
    <ul><li>переход от натурального хозяйства к производству товаров и обмену товарами;</li>
      <li>имущественное обособление производителей товаров - собственников изготовляемой продукции.</li>
