<hr>
    <b>Сущность денег раскрывается в пяти функциях:</b>

    <ul>
      <li><b>Мера стоимости</b> формируется при образовании цены, она определяет стоимость товара, которая измеряется
        деньгами (т. е. приравнивание товаров между собой). Таким образом получают
        количественное соизмерение.

        Денежное измерение стоимости — цена. Она зависит от нескольких условий:<br>
        - условия производства;<br>
        - условия обмена.<br>
        В качестве меры стоимости деньги могут действовать как счетные, выступая в виде числовых величин. Счетные деньги
        применяются для выражения цены, учета и анализа, ведения счетов
        участников хозяйственной жизни.
      </li>

      <li><b>Средства обращения</b>. Денежное выражение стоимости товаров еще не означает его реализацию. Должен
        произойти
        обмен. Деньги — посредники при обмене от начала сделки (Т — Д) до ее
        завершения (Д — Т). В период преобладания торговли деньги в основном действовали как средство обращения; после
        возникновения кредита и развития экономики на первый план выходит функция
        средства платежа, которая включает в себя функцию средства обращения и трансформируется в функцию средства
        расчетов. Этому способствует использование пластиковых карточек и других
        электронных инструментов расчетов, позволяющих расплачиваться путем перечисления с банковского счета, а также
        осуществления оптовых и розничных покупок.
      </li>

      <li><b>Средства платежа</b> - время платежа не совпадает со временем оплаты, товары продают в кредит, с отсрочкой
        платежа (Т — О и О — Д).
      </li>

      <li><b>Средства накопления</b> — денежный резерв (остатки на счетах, золотовалютные резервы). Деньги, выполняющие
        функцию накопления, участвуют в процессе формирования, распределения,
        перераспределения национального дохода, образования сбережений населения.
      </li>

      <li><b>Мировые деньги</b> используются в международных расчетах.</li>
    </ul>
    В современной развитой экономике действуют три функции денег — мера стоимости, средства накопления и средства расчетов, а средство обращения остается в очень небольших размерах.<br>
    <b>Функции денег</b> — это концентрированное выражение их роли в хозяйстве.<br>
    Деньги располагают таким разнообразным набором свойств, что возникает необходимость их классифицировать, выделив целый ряд функций. Каждая из функций денег описывает более или
    менее однородный круг экономических операций, выполняемых с помощью этой функции. При этом следует иметь в виду, что деньги — это не сумма функций, и выполняя какую-либо одну функцию,
    они сохраняют свое единство и содержат в себе все остальные функции.<br>
    Функции денег находятся в постоянной динамике: некоторые возникли раньше, некоторые позже; отдельные функции сильно изменили свое содержание и даже утратили заметное значение.<br>
    Возникновение функций денег в процессе их эволюции можно представить следующим образом:<br>
    <b>I этап. </b>Деньги как мера стоимости. Исторически первая функция денег. В качестве меры стоимости деньги являются унифицированным измерителем стоимостей всех товаров.<br>
    <b>II этап.</b> Деньги как покупательное средство. Деньги как покупательное средство представляют собой средство обращения.<br>
    <b>III этап.</b> Деньги как средство платежа. В функции денег как средства платежа возникает временной лаг (несовпадение во времени) между продажей товара и получением денег за него. В этих
    условиях объективно создаются условия для такого экономического явления, как кредит.<br>
    <b>IV этап.</b> Деньги как средство распределения.В распределительной функции денег есть только их движение от их владельца к получателю. В этой функции состоит объективная
    экономическая предпосылка для возникновения государственных финансов.<br>
    <b>V этап.</b> Деньги как средство накопления и сбережения. Процесс сбережений и накоплений — необходимый элемент современной экономики.<br>
    <b>VI этап.</b> Деньги как мера обмена одной валюты на другую. В функции мировых денег деньги способствуют валютному обмену, созданию платежного баланса, образованию валютного курса.<br>
<hr>
    <b>1.</b> Роль денег, прежде всего, проявляется в результатах участия денег в установлении цены товара. В условиях рыночной экономики эта величина складывается, исходя из стоимости товара,
    с возможным отклонением цены от стоимости. На цену товара влияют соотношение спроса и предложения и конкуренция, что позволяет снижать цену товара. Однако снижение цен могут допустить 
    производители, у которых уровень издержек ниже. Напротив, производители, у которых уровень издержек выше, вынуждены либо добиться снижения издержек, либо сократить или прекратить производство 
    таких товаров. Механизм ценообразования направлен, следовательно, на повышение эффективности производства, на снижение уровня издержек.<br><br>
    <b>2. </b>Большое значение имеют деньги в процессе денежного оборота, когда они выполняют функцию средства обращения или средства платежа. При оплате приобретаемых ценностей или оказанных услуг
    покупатель контролирует уровень цен и качество товаров и услуг, что вынуждает изготовителей снижать цены и повышать качество своей продукции. В итоге это направлено на повышение эффективности 
    производства.<br><br>
    <b>3. </b>Общественная роль денег, их функция в экономической системе состоит в том, что они выступают в качестве связующего звена между товаропроизводителями.<br>
    Будучи конкретизированными в определенном предмете, они являются как бы всеобщим условием общественного производства, «инструментом» общественных экономических связей независимых
    товаропроизводителей, орудием стихийного учета общественного труда в товарном хозяйстве.    <br><br>
    <b>4.</b> Деньги играют и качественно новую роль - они становятся капиталом, или самовозрастающей стоимостью.<br><br>
    <b>5.</b> С помощью денег происходит образование и перераспределение национального дохода через государственный бюджет, налоги, займы и инфляцию.<br><br>
    <b>6. </b>Деньги играют важную роль в хозяйственной деятельности предприятий, в функционировании органов государства, в усилении заинтересованности людей в развитии и повышении эффективности производства, 
    экономном использовании ресурсов.<br><br>
    С помощью денег можно определить не только суммарную величину издержек (материалы, амортизация, электроэнергия, зарплата и др.) на производство каждого вида продукции и совокупного
    их объема, но и результаты производства посредством цены отдельных видов продукции, всего ее объема, величину полученной прибыли.<br>
    Без использования денег, лишь по различным натуральным показателям определить совокупный объем затрат на изготовление отдельных видов продукции и совокупного ее объема не представляется
    возможным. Применение денег позволяет сопоставить выручку от реализации продукции и отдельных ее видов с издержками на ее производство, оценить выгодность производства каждого вида
    продукции. Тем самым создаются предпосылки для усиления заинтересованности в расширении производства наиболее выгодных видов продукции, что направлено на снижение издержек, рост
    прибыли и повышение эффективности производства.
