  <h3>
    <b>Понятие денежной системы</b>. Представление о содержании, типах и элементах денежной системы вытекает из исторического анализа процесса эволюции денег. Как было выяснено,
    в процессе развития идет постепенный переход от товарных денег к металлическим, от металлических — к бумажным. В настоящее время бумажные деньги претерпевают
    качественное изменение в направлении к виртуальным формам выражения.<br><br>

    <b>Денежная система</b> представляет собой форму организации денежного обращения в стране, исторически сложившуюся и закрепленную национальным законодательством.<br>
    Тип денежной системы зависит от того, в какой форме функционируют деньги: как товар всеобщий эквивалент или как знаки стоимости. В связи с этим выделяют следующие типы денежных систем:<br>
    <ul>
      <li>Система металлического денежного обращения;</li>
      <li>Система обращения кредитных и бумажных денег.</li>
    </ul>
    <b>Денежные системы металлического обращения</b> базируются на <b>металлических деньгах</b> (золотых, серебряных), которые выполняют все функции, присущие деньгам как всеобщему 
    эквиваленту (меры стоимости, средства обращения и платежа, средства накопления), а обращающиеся одновременно с металлическими деньгами банкноты могут быть в любой момент 
    времени обменены на металлические деньги.<br>

    <b>Денежные системы бумажно-кредитного обращения</b> базируются на господстве бумажных или кредитных денег.<br><br>
    В условиях существования металлического денежного обращения различали два типа денежных систем: <b>биметаллизм</b> и <b>монометаллизм</b>.<br>

    <b>Биметаллизм</b> представляет собой денежную систему, при которой государство законодательно закрепляет роль всеобщего эквивалента за двумя металлами, обычно золотом и серебром. <br>
    Монеты из золота и серебра функционируют на равных основаниях, предусматривается их свободная чеканка. На рынке устанавливались две цены (в золоте и в серебре) на один и тот же товар.
    Существовали три разновидности биметаллизма:<br>

    <ul>
      <li>Система параллельной валюты, когда соотношение между золотыми и серебряными монетами устанавливалось стихийно
        на рынке;</li>
      <li>Система двойной валюты, когда это соотношение устанавливалось государством;</li>
      <li>Система хромающей валюты, при которой золотые и серебряные монеты служат законным платежным средством, но не на
        равных основаниях, так как чеканка серебряных монет производилась в закрытом порядке в отличие от свободной
        чеканки золотых монет, в таком случае серебряные монеты становились знаком золота.</li>
    </ul>
    Наличие двух металлов в роли всеобщего эквивалента вступало в противоречие с экономической сущностью денег как единого товара, призванного осуществлять измерение стоимости всех других товаров.
    Развитие экономических отношений требовало стабильности денежной системы, не подверженной колебаниям стоимости одного из денежных металлов.<br><br>
    <b>Монометаллизм</b> представляет собой денежную систему, при которой один денежный металл является всеобщим эквивалентом и основой денежного обращения. Одновременно с металлическими
    деньгами в обращении присутствуют другие знаки стоимости (банкноты, казначейские билеты, разменная монета), разменные на золото. Истории известны серебряный и золотой монометаллизм.
    Золотой монометаллизм, или золотой стандарт, существовал в виде золотомонетного, золотослиткового и золотодевизного стандартов.<br>
    При золотомонетном стандарте золото выполняет все функции денег, в обращении находятся как золотая монета, так и знаки золота; производится свободная чеканка золотых монет с
    фиксированным содержанием золота; золотые монеты свободно меняются на знаки золота по нарицательной стоимости.<br>
