        денежной
        единице ( это определение утратило экономическое значение, т.к.
        кредитные деньги не имеют собственной стоимости и не могут быть выразителем стоимости других товаров). А в 1961
        г.
        1 руб. приравнивался к 0.98741 г золота.
      </li>

      <li><b>Виды денег, являющиеся законными платежными средствами</b> - это прежде всего кредитные деньги и банкноты,
        разменная монета, а также бумажные деньги (казначейские билеты).
      </li>

      <li><b>Эмиссионная система</b> - законодательно установленный порядок выпуска и обращения денежных знаков.
        Эмиссионные операции осуществляет Центральный банк (выпуск банковских билетов)
        и казначейство (выпуск казначейских билетов, мелко купюрных бумажно-денежных знаков). Эмиссия банкнот
        осуществляется тремя путями: предоставлением кредитов кредитным учреждениям в форме
        переучета коммерческих векселей; кредитованием казны под обеспечение государственных ценных бумаг; выпуском
        банкнот путем их обмена на иностранную валюту.
      </li>

      <li><b>Регулирование денежного обращения</b>. Мерой регулирования прироста денежной массы и кредита является
