  <h1>Вопрос:
    Информационное обеспечение финансового менеджмента организации.
    Показатели информационного обеспечения финансового менеджмента организации.
    Состав финансовой отчетности организации для принятия управленческих решений.</h1>
  <hr>
  <h3>
    Эффективность финансового менеджмента организации в значительной мере определяется используемой информационной базой и зависит от нее. <b>Информационное обеспечение</b> финансового
    менеджмента представляет собой совокупность информационных ресурсов организации, используемых в процессе принятия управленческих решений. В систему информационного обеспечения
    входят нормативно-правовые акты, статистические и справочные данные, а также финансовая отчетность организации.<br>
    Информационное обеспечение организации формируется за счет внешних и внутренних источников.<br>
    <b>Внешние:</b><br>
    <b>1. Макроэкономические показатели</b> – служат основой проведения анализа и прогнозирования условий внешней среды функционирования организации для принятия стратегических решений в области финансовой деятельности.<br>
    <b>2. Показатели, характеризующие конъюнктуру рынка.</b> Система нормативных показателей этой группы служит для принятия управленческих решений в области формирования портфеля долгосрочных финансовых инвестиций, осуществления краткосрочных финансовых вложений и некоторых других аспектов финансового менеджмента.<br>
    <b>3. Показатели, характеризующие деятельность контрагентов и конкурентов.</b> Источником формирования показателей этой группы служат публикации отчетных материалов в прессе (по отдельным видам хозяйствующих субъектов такие публикации являются обязательными), соответствующие рейтинги с основными результативными показателями деятельности (по банкам, страховым компаниям), а также платные бизнес-справки, предоставляемые отдельными информационными компаниями (получение такой информации должно осуществляться только легальными способами).<br>
    <b>4. Нормативно-правовые акты</b> – учитываются в процессе подготовки финансовых решений, связанных с особенностями государственного регулирования финансовой деятельности организации.<br>

    <b>Внутренние:</b><br>
    Система <b>внутренней информации</b> характеризует финансовое состояние организации в текущем времени.<br>
    <b>Показатели, характеризующие финансовое состояние и результаты финансовой деятельности организации в целом</b> – применяются в процессе финансового анализа, планирования,
    разработки финансовой стратегии и политики по основным аспектам финансовой деятельности. Формирование системы показателей этой группы основывается, прежде всего, на данных
    финансового учета организации. <br>
    <b>Показатели, характеризующие финансовые результаты деятельности отдельных структурных подразделений организации.</b> Система этой группы показателей используется для текущего
    и оперативного управления финансовой деятельностью организации. Формирование системы показателей этой группы основывается, прежде всего, на данных организуемого в организации
    управленческого учета.    <br>
    <b>Нормативно-плановые показатели, связанные с финансовым развитием организации.</b> Эти показатели используются в процессе текущего и оперативного контроля за ходом осуществления финансовой деятельности и формируются по следующим двум блокам:<br>
    1. система нормативов, регулирующих финансовое развитие организации (нормативы соотношения отдельных видов активов и структуры капитала, нормативы удельного расхода финансовых ресурсов и затрат и т.п.); <br>
    2. система плановых показателей финансового развития организации. В состав показателей этого блока включается вся совокупность показателей текущих и оперативных финансовых планов всех видов.    <br>
    <br>
    В системе информационного обеспечения особую роль играет финансовая (бухгалтерская) отчетность организации. Она представляет собой достаточно объективную и достоверную
    информационную базу, с помощью которой можно сформировать мнение об имущественном и финансовом положении организации. В соответствии с действующим законодательством
    бухгалтерская отчетность является открытым источником информации, а ее состав и формы представления унифицированы.<br>
    <hr>
