    Система показателей информационного обеспечения финансового менеджмента, формируемых из внешних источников, делится на две основные группы:<br>
    1. Показатели, формируемые из внешних источников.<br>
    2. Показатели, формируемые из внутренних источников.   <br>
    <b>Показатели информационного обеспечения финансового менеджмента предприятия, формируемые из внешних источников:</b>
<ul>
  <li>Показатели, характеризующие общеэкономическое развитие страны. Формирование системы показателей этой группы основывается на публикуемых данных государственной статистики.</li>
  <li>Показатели, характеризующие конъюнктуру финансового рынка. Формирование системы показателей этой группы основывается на публикациях периодических коммерческих изданий, фондовой и валютной биржи, а также на соответствующих электронных источниках информации.</li>
  <li> Показатели, характеризующие деятельность контрагентов и конкурентов. <br>Источником формирования показателей этой группы служат публикации отчетных материалов в прессе (по отдельным видам хозяйствующих субъектов такие публикации являются обязательными), соответствующие рейтинги с основными результативными показателями деятельности (по банкам, страховым компаниям), а также платные бизнес-справки, предоставляемые отдельными информационными компаниями</li>
  <li>  Нормативно-регулирующие показатели. Источником формирования показателей этой группы являются нормативно-правовые акты, принимаемые различными органами государственного управления</li>
</ul>
    <br>
    <b>Показатели информационного обеспечения финансового менеджмента предприятия, формируемые из внутренних источников:</b>
<ul>
  <li>Показатели, характеризующие результаты основной, инвестиционной, финансовой деятельности. Формирование системы показателей этой группы основывается на данных финансового учета предприятия. </li>
  <li>Показатели финансового состояния предприятия. </li>
  <li>   Показатели, характеризующие результаты деятельности различных структурных подразделений предприятия. Формирование системы показателей этой группы основывается на данных организуемого на предприятии управленческого учета</li>
  <li>  Нормативно-плановые показатели, связанные с деятельностью предприятия</li>
</ul>
    <!----------------------------------------------------------- часть 3 --------------------------------------------->

    <hr>
    Отчётность позволяет получать исчерпывающую информацию, необходимую внутренним и внешним пользователям этой отчетности для принятия управленческих решений. Результатом ее
    анализа может быть составление мнения о целесообразности использования или реорганизации организационной структуры предприятия и эффективности управления его менеджерами.
    Поэтому финансовая отчетность должна содержать информацию об имуществе и обязательствах, доходах и расходах, прибылях и убытках предприятия, активах и пассивах и включать
    показатели деятельности всех подразделений экономического субъекта, включая его филиалы и представительства, независимо от их места нахождения.<br>
        Бухгалтерская отчетность содержит нужную информацию в разных бухгалтерских отчетах. Данные о финансовом положении, а именно информация об ресурсах и источниках их
    финансирования содержится в бухгалтерском балансе. Информация о достигнутых финансовых результатах содержится в отчете о прибылях и убытках. Информация о денежных потоках
    показана в отчете о движении денежных средств. Все финансовые отчеты взаимосвязаны между собой и показывают, как хозяйственные операции изменяют финансовое положение предприятия.<br>
    <b>Полный комплект бухгалтерской отчетности включает:</b>
    <ul>

      <li>Бухгалтерский баланс (Отчет о финансовом положении)</li>
      <li> Отчет о прибылях и убытках (Отчет о финансовых результатах)</li>
      <li> Отчет о движении денежных средств</li>
      <li> Отчет об изменениях капитала</li>
      <li> Примечания и приложения</li>
      <li> Пояснительная записка</li>
      <li> Аудиторское заключение</li>
    </ul><br>
    Промежуточная бухгалтерская отчетность (за месяц или квартал), а также упрощенная бухгалтерская отчетность (для малых предприятий) ограничивается первыми двумя или тремя отчетами.<br>
    <b>Бухгалтерский баланс</b><br>
    Бухгалтерский баланс – экономическая группировка имущества по его составу и источникам финансирования. Баланс показывает финансовое положение организации на отчетную дату.<br>
    Информация об экономических ресурсах и правах требования помогает пользователям определить сильные и слабые стороны отчитывающейся организации с финансовой точки зрения.
    Эта информация поможет оценить ликвидность и платежеспособность, потребность в источниках финансирования и вероятность их получения, а также оценить эффективность
    управления ресурсами.<br>
    Бухгалтерский баланс представляет собой двустороннюю таблицу. В верхней части баланса показывается имущество по составу и размещению (активы баланса), а в нижней части
    отображаются источники формирования этого имущества (пассивы баланса). Также возможен вариант, если активы расположены в левой части таблицы, а пассивы – в правой. <br>
<br>
    <b>Отчет о финансовых результатах (Отчет о прибылях и убытках)</b>

    Отчет о прибылях и убытках показывает финансовые результаты деятельности организации за отчетный период. Финансовый результат показывает разницу между доходами и расходами.
    Если доходов больше – получается прибыль, расходов больше – убыток. Отчет содержит данные о доходах и расходах нарастающим итогом с начала периода до отчетной даты.<br>

    Стандартно в отчете о финансовых результатах доходы и расходы отражаются по методу начисления (кассовый метод - исключение для малых предприятий). Выручка начисляется, когда
    покупателю были отгружены товары или оказаны услуги. Учет по методу начисления отражает доходы и расходы в том периоде, в котором они возникли, даже если денежные поступления
    и выплаты были в другом периоде.<br><br>
    Отчет о финансовых результатах помогает понять, какую отдачу организация получила от имеющихся у нее экономических ресурсов. Эта информация поможет оценить эффективность управления ресурсами.<br>
    <br><b>Отчет о движении денежных средств (ДДС)</b>
    Информация о денежных потоках помогает оценить способность предприятия генерировать чистые поступления денежных средств и управление ресурсами. Отчет показывает, как
    отчитывающаяся организация получает и расходует денежные средства, включая информацию о ее заимствованиях и погашении задолженности, дивидендах и прочих факторах, которые
    влияют на ликвидность и платежеспособность. Информация о денежных потоках помогает понять осуществляемые операции, дать оценку финансовой и инвестиционной деятельности,
    оценить ее ликвидность или платежеспособность.<br>
    Структура отчета о движении денежных средств классифицирует денежные потоки по видам деятельности:<br>
    - Операционная деятельность<br>
    - Инвестиционная деятельность<br>
    - Финансовая деятельность<br>
    Операционная деятельность показывает способность предприятия быть на самофинансировании.<br>
    Инвестиционная деятельность показывает денежные потоки во внеоборотные активы.<br>
    Финансовая деятельность показывает денежными потоки в собственный и заемный капитал.<br>

    <br><b>Отчет о движении собственного капитала</b><br>
    В отчете раскрывается информация о движении уставного капитала, резервного капитала, добавочного капитала, а также информация об изменениях величины нераспределённой прибыли
    (непокрытого убытка) организации и доли собственных акций, выкупленных у акционеров. Изменения в капитале предприятия между двумя отчетными датами отражают увеличение или
    уменьшения его чистых активов за этот период. <br>

    <br><b>Пояснения к бухгалтерской (финансовой) отчетности</b><br>
    Помимо финансовой отчетности предприятия представляют пояснительную записку с примечаниями, в которой описываются и объясняются основные характеристики финансовых результатов
    и финансового положения предприятия.<br>
    Предприятие должно раскрыть примечаниях более подробную разбивку статей с использованием классификации, которая подходит для деятельности предприятия.<br>
    Примечания должны:<br>
    - представлять информацию об используемой учетной политике<br>
    - раскрывать информацию, которая не представлена в отчетности<br>
    Агрегирование — это объединение статей с общими характеристиками и относящихся к одной классификационной группе. Агрегирование делает информацию наглядной за счет
    обобщения массива данных, однако приводит к сокрытию деталей. В финансовой отчетности требуется различная степень агрегирования. В отчетах представляется обобщенная
    информация, а в примечаниях - подробная.<br>


