  <h1>Вопрос:
    Финансовые инструменты в финансовом менеджменте.
    Классификация финансовых инструментов в финансовом менеджменте.
    Краткая характеристика наиболее часто применяемых финансовых инструментов</h1>
  <hr>
  <h3>
    Осуществляя операции на финансовом рынке, предприятие выбирает соответствующие финансовые инструменты их проведения. Финансовые инструменты представляют собой разнообразные
    обращающиеся финансовые документы, имеющие денежную стоимость, с помощью которых осуществляются операции на финансовом рынке
  <p></p>
    <b>Финансовый инструмент</b> — контракт, по которому происходит одновременное увеличение финансовых активов одного предприятия (организации, коммерческой структуры) и финансовых обязательств долгового или долевого характера другого предприятия (организации, коммерческой структуры).
<p></p>

  </h3>
