  <h1>Вопрос:
    Общие требования к характеристике кредита как экономической категории.
    Сущность и функции кредита.
    Принципы кредита как экономической категории
  </h1>
  <hr>
  <h3>
    <B>Кредит как экономическая категория </B>представляет собой определенный вид общественных отношений, связанных с движением стоимости на условиях возвратности. Кредит может выступать в товарной
    и денежной формах. В товарной форме он предполагает передачу во временное пользование стоимости в виде конкретной вещи, определенной родовыми признаками. В современной экономической системе
    преобладает денежная форма кредита. Это означает, что кредит предоставляется и погашается в денежной форме. Участие денег в опосредствовании кредитных отношений не лишает их специфических
    черт и не превращает кредит в экономическую категорию «деньги». В кредитной сделке нет эквивалентного товарно-денежного обмена, а есть передача стоимости во временное пользование с условием
    возврата через определенное время и уплаты процентов за пользование этой стоимостью. Возвратность ссуженной стоимости, которую нельзя отменить волей одного из субъектов кредитной сделки, и
    представляет гобой неотъемлемую черту кредита как экономической категории.<br>

    <!--    -->

    <hr>
    <B>Сущность кредита </B>состоит в экономических отношениях, которые складываются между тем, кто его предоставляет (кредитором), и тем, кто его берет (заемщиком).<br>
    Эти отношения представляют собой передачу материальных ценностей в денежной или иной форме, основываясь на следующих свойствах займа:<br>
    - возвратности,
    - срочности,
    - платности.<br>
    Под <B>возвратностью</B> подразумевается, что кредит необходимо возвращать тому, кто его выдал, т. е. материальные ценности передаются на время. Под
    <B>срочностью</B> понимается то, что заранее
    установлен срок возврата. Как правило, это определяется в договоре между кредитором и заемщиком. За использование кредита выплачивается вознаграждение в виде процента, указанного в соглашении,
    что означает его <B>платность</B>.<br>

    Субъектами кредитных отношений выступают кредиторы и заемщики. При этом <B>кредитор</B> — это тот, кто предоставляет те или иные ресурсы во временное пользование. А
    <B>заемщик</B> — это субъект кредитных отношений, который получает средства и обязуется их возвратить в определенный срок.<br>

    Особое место в современных кредитных отношениях занимают специализированные кредитные организации, которые могут выступать одновременно как кредиторами, так и заемщиками.
    Такая деятельность по привлечению и размещению средств в большинстве случаев служит для них основным видом деятельности.<br><br>

    <B>Функции кредита </B>– проявление сущности кредита, выражение его общественного назначения. Функции кредита относятся к кредитным отношениям в целом, а не к отдельным взаимоотношениям
    заемщика и кредитора.<br>
    Принято выделять <B>три</B> функции кредита.<br>

    - <B>распределительная</B> функция, которая заключается в том, что благодаря кредитным отношениям временно свободные денежные средства перераспределяются в пользу тех, кто в них нуждается.
    Таким образом осуществляется переход активов в более эффективные отрасли экономики.<br>
    - <B>замещения</B>. Функция замещения заключается в замещении наличных денег кредитными знаками стоимости и обращения в виде векселей, чеков, кредитных карточек и безналичных расчетов. Использование безналичных
    расчетов по денежным обязательствам и зачет взаимной задолженности значительно сокращает налично-денежный оборот и уменьшает издержки наличного денежного обращения.<br>
    - <B>Функция аккумуляции и концентрации денежного капитала</B> выполняет следлующую роль. Источниками капитала могут быть собственные средства в виде прибыли и заемные средства в виде кредита. 
    Получение прибыли требует длительного времени, а получение кредита в банке возможно в течение нескольких дней, поэтому денежные средства, взятые в кредит, сокращают время для накопления капитала.

    <!--    -->
    <hr>
    <B>К основным принципам кредитования относятся:</B><br>

    - <B>Возвратность кредита.</B> Обязательный признак кредита, без него теряется суть кредитования. Этот принцип выражает необходимость своевременного возврата полученных от кредитора финансовых
    средств после завершения их использования заемщиком. Он находит свое практическое выражение в погашении конкретной ссуды путем перечисления соответствующей суммы денежных средств на счет
    предоставившей ее кредитной организации (или иного кредитора), что обеспечивает возобновляемость кредитных ресурсов банка как необходимого условия продолжения его деятельности.<br>

    <B>- Срочность кредита. </B>Способ достижения возвратности кредита. Он отражает необходимость его возврата не в любое приемлемое для заемщика время, а в точно определенный срок,
    зафиксированный в кредитном договоре или заменяющем его документе. Нарушение указанного условия является для кредитора достаточным основанием для применения к заемщику экономических санкций
    в форме увеличения взимаемого процента, а при дальнейшей отсрочке (в нашей стране - свыше трех месяцев) - предъявления финансовых требований в судебном порядке.<br>

    <B>- Платность кредита. Ссудный процент.</B><br> Этот принцип выражает необходимость не только прямого возврата заемщиком полученных от банка кредитных ресурсов, но и оплаты их
    использования. Экономическая сущность платы за кредит отражается в фактическом распределении дополнительно полученной за счет его использования прибыли между заемщиком и кредитором.
    Практическое выражение рассматриваемый принцип находит в процессе установления величины банковского процента, выполняющего три основные функции:<br>
    - <B>перераспределение</B> части прибыли юридических и дохода физических лиц;<br>
    - <B>регулирование</B> производства и обращения путем распределения ссудных капиталов на отраслевом, межотраслевом и международном уровнях;<br>
    - на кризисных этапах развития экономики - <B>антиинфляционную защиту</B> денежных накоплений клиентов банка.<br>

