  <h1>Вопрос:
    Платежный и расчетный балансы страны.
    Принципы составления, способы регулирования платежного баланса страны.
    Структура и содержание статей платежного баланса страны.
  </h1>
  <hr>
  <h3>
    <b>Платежный баланс</b> — это макроэкономический показатель, являющийся разностью между суммой платежей, поступающих из-за границы, и платежей, отправленных за границу страны. Фактически
    Balance of payments отображает итог внешнеэкономических операций, в том числе и по договорам экспорта и импорта, трансфертных платежей и т.д. Платежный баланс считается положительным
    или активным, когда сумма поступивших платежей больше суммы выплат.<br>

    <b>Расчетный баланс</b> — это соотношение между всеми долговыми требованиями, которые имеет страна по отношению к другим государствам, и долговыми обязательствами, которые страна должна выполнить
    по отношению к другим странам. Фактически это соотношение между возможным поступлением валюты и необходимыми ее выплатами за определенный период.

   <hr>
    В основе составления платежного баланса лежат два основных принципа:<br>

    <b>1. </b>охват всех внешнеэкономических операций страны за определенный период (месяц, квартал, год);<br>

    <b>2. </b>двойная бухгалтерская запись операций (кредит, в конечном счете, должен совпадать с дебетом), в результате чего дисбаланс по одним статьям балансируется другими статьями.<br>

    <b>Так же в ПБ включаются</b>, так называемые, трансфертные операции, которые не влекут за собой адекватной компенсации в той или иной форме (т.е. в форме товаров, услуг, активов). В этом случае
    в торговом балансе автоматически записывается одна сторона операции, а компенсационная запись будет отражена в графе трансфертов.<br>

    <b>Система двойной записи предполагает наличие результирующего нулевого сальдо платежного баланса</b>. Однако на практике из-за сложности полного охвата сделок, неоднородности цен, разницы во 
    времени регистрации сделок и других причин неизбежны расхождения, сумма которых уравновешивает общий результат платежного баланса и отражается в статье «Ошибки и пропуски». По критериям
    МВФ считается, что баланс составлен удовлетворительно, если сумма, проходящая по графе «Ошибки и пропуски», не превышает 5% экспорта или импорта данной страны.<br><br>

    <b>Рассмотрим основные методы регулирования платежного баланса.</b><br>

    <b>1. Дефляционная политика. </b>Такая политика, направленная на сокращение внутреннего спроса, включает ограничение бюджетных расходов преимущественно на гражданские цели, замораживание цен и 
    заработной платы. Одним из важнейших ее инструментов служат финансовые и денежно-кредитные меры: уменьшение бюджетного дефицита, изменения учетной ставки центрального банка (дисконтная политика), 
    кредитные ограничения, установление пределов роста денежной массы. В условиях экономического спада, при наличии большой армии безработных и резервов неиспользованных производственных 
    мощностей политика дефляции ведет к дальнейшему падению производства и занятости. Она связана с наступлением на жизненный уровень и грозит обострением социальных конфликтов, если не 
    принимаются компенсирующие меры.<br>

    <b>2. Девальвация. </b>Понижение курса национальной валюты направлено на стимулирование экспорта и содержание импорта товаров. Однако роль девальвации в регулировании платежного баланса зависит от
    конкретных условий ее проведения и сопутствующей общеэкономической и финансовой политики. Девальвация стимулирует экспорт товаров лишь при наличии экспортного потенциала конкурентоспособных 
    товаров и услуг и благоприятной ситуации на мировом рынке. Удорожая импорт, девальвация может привести к росту издержек производства импортных товаров, повышению цен в стране и последующей 
    утрате полученных с ее помощью конкурентных преимуществ на внешних рынках. Поэтому хотя она может дать стране временные преимущества, но во многих случаях не устраняет причины дефицита 
    платежного баланса.<br>

    <b>3. Валютные ограничения. </b>Блокирование инвалютной выручки экспортеров, лицензирование продажи иностранной валюты импортерам, сосредоточение валютных операций в уполномоченных банках направлены 
    на устранение дефицита платежного баланса путем ограничения экспорта капитала и стимулирования его притока, сдерживания импорта товаров.<br>

    <b>4. Финансовая и денежно-кредитная политика. </b>Для уменьшения дефицита платежного баланса используются бюджетные субсидии экспортерам, протекционистское повышение импортных пошлин, отмена 
    налога с процентов, выплачиваемых иностранным держателям ценных бумаг в целях притока капитала в страну, денежно-кредитная политика.<br>

    <b>5. Специальные меры государственного воздействия </b>на платежный баланс в ходе формирования его основных статей - торгового баланса, «невидимых» операций, движения капитала.<br><br>

    Важным объектом регулирования является <b>торговый баланс. </b>В современных условиях государственное регулирование охватывает не только сферу обращения, но и производства экспортных товаров.
    Стимулирование экспорта на стадии реализации товаров осуществляется путем воздействия на цены (предоставление экспортерам налоговых, кредитных льгот, изменение валютного курса и т.д.). 
    Для создания долгосрочной заинтересованности экспортеров в вывозе товаров и освоении внешних рынков государство предоставляет целевые экспортные кредиты, страхует их от экономических и
    политических рисков, вводит льготный режим амортизации основного капитала, предоставляет им иные финансово-кредитные льготы в обмен на обязательство выполнять определенную экспортную программу.    

<!--    -->
    <hr>
    <b>Платежным балансом </b>называется соотношение между суммой всех платежей, произведенных данной страной другим странам за определенный период, и суммой всех поступлений, 
    полученных ею за тот же период от других стран. Баланс, в котором поступления денежных средств превышают их расходование, называют активным, в противоположном случае - пассивным.<br>

    <b>Структура платежного баланса</b><br>
    <b>Платежный баланс имеет следующие разделы :</b><br>

    <ul>
      <li>торговый баланс, т.е. соотношение между вывозом и ввозом товаров;</li>
      <li>баланс услуг и некоммерческих платежей (баланс «невидимых» операций);</li>
      <li>баланс движения капиталов и кредитов</li>
    </ul>

    <b>Торговый баланс</b>
    Соотношение стоимости экспорта и импорта товаров образует торговый баланс. Для стран, отстающих от лидеров по уровню экономического развития, активный торговый баланс необходим
    как источник валютных средств для оплаты международных обязательств по другим статьям платежного баланса. Пассивный торговый баланс считается нежелательным и обычно оценивается
    как признак слабости внешнеэкономических позиций станы. Это правильно для развивающихся стран, испытывающих нехватку валютных поступлений .<br><br>

    <b>Баланс услуг </b>включает платежи и поступления по транспортным перевозкам, страхованию, электронной, телекосмической, и т. д.<br>
    По принятым в мировой статистике правилам в раздел «услуги» входят выплаты доходов по инвестициям за границей и процентов по международным кредитам, хотя по экономическому
    содержанию они ближе к движению капиталов и услуг. В платежном балансе выделяются статьи: предоставление военной помощи иностранным государствам, военные расходы за рубежом.<br>
    По методике МВФ принято также показывать особой позицией в платежном балансе односторонние переводы. В их числе: государственные операции - субсидии другим странам по линии экономической
    помощи, государственные пенсии, взносы в международные организации; частные операции - переводы иностранных рабочих, специалистов, родственников на родину. Перечисленные операции услуг,
    движения доходов от инвестиций , сделки военного характера и односторонние переводы называют «невидимыми» операциями, подразумевая, что они не относятся к экспорту и импорту товаров,
    т.е. осязаемых ценностей..<br>

    <b>Баланс движения капиталов и кредитов </b>выражает соотношение вывоза и ввоза государственных и частных капиталов, предоставленных и полученных международных кредитов. По экономическому
    содержанию эти операции делятся на две категории: международное движение предпринимательского и ссудного капитала.

