  <h1>Вопрос:
    Валютные отношения и валютная система.
    Понятие, категории, элементы валютной системы России.
    Валютные отношения в Мировой валютной системе.
  </h1>
  <hr>
  <h3>
    <b>Валютные отношения </b>– совокупность общественных отношений, складывающихся при функционировании валюты в мировом хозяйстве и обслуживающих взаимный обмен результатами деятельности
    национальных хозяйств.<br>
    Отдельные элементы валютных отношений появились еще в Древней Греции и в Древнем Риме в виде <b>вексельного</b> и
    <b>меняльного</b> дела. Следующей вехой их развития явились средневековые "вексельные ярмарки" в Лионе, Антверпене и других торговых центрах Западной Европы, где производились расчеты по 
    переводным векселям (<b>траттам</b>). В эпоху феодализма и становления капиталистического способа производства стала развиваться система международных расчетов через банки.<br>

    Развитие международных валютных отношений обусловлено ростом производительных сил, созданием мирового рынка, углублением международного разделения труда, формированием мировой системы 
    хозяйства, интернационализацией и глобализацией хозяйственных связей.<br>

    Международные валютные отношения опосредствуют международные экономические отношения, которые относятся как к сфере материального производства, то есть к первичным производственным 
    отношениям, так и к сфере распределения, обмена, потребления.<br>

    Международные валютные отношения постепенно приобрели определенные формы организации на основе интернационализации хозяйственных связей.<br>

    <b>Валютная система</b> – форма организации и регулирования валютных отношений, закрепленная национальным законодательством или межгосударственными соглашениями.<br>
    <b>Валютные отношения</b> представляют собой разновидность денежных отношений, возникающих при функционировании денег в международном обороте.

   <hr>
    <b>ВАЛЮТНЫЕ СИСТЕМЫ:</b><br>
    - НАЦИОНАЛЬНАЯ<br>
    - МИРОВАЯ<br>
    - РЕГИОНАЛЬНАЯ<br><br>

    <b>Национальная валютная система</b> - это совокупность экономических отношений, обеспечивающих международный оборот, формирующих и использующих валютные ресурсы, необходимые для
    воспроизводственного процесса.

    Она сформировалась в рамках национальной денежной системы, которая регламентировала порядок денежных расчетов данной страны другими государствами и постепенно от нее обособилась.<br>

    Изначально возникли национальные валютные системы. Они являлись частью денежной системы страны, основывались на национальном законодательстве, но учитывали нормы международного права.
    Для национальной валютной системы характерным является ее регулирование внутри самого государства, через систему мер, определяющих: <br>
    - конвертируемость национальной денежной единицы, <br>
    - условия функционирования внутреннего валютного рынка и рынка золота, <br>
    - режим валютного курса, <br>
    - международную валютную ликвидность страны, <br>
    - формы международных расчетов, <br>
    - режим валютных ограничений.<br>
    В зависимости от уровня развития экономики и международных экономических связей национальная валютная система выходила за пределы одного государства и тем самым способствовала зарождению
    мировой и региональных валютных систем. Характерной чертой последних является регулирование валютных систем на межгосударственном уровне. Если регулированием национальных систем заняты
    национальные органы (Центральный банк, Министерство финансов и т. д. конкретной страны), то мировые и региональные валютные системы регулируются международными организациями
    (Международный валютный фонд, Европейский центральный банк и т. д.).<br><br>

    <b>Региональная валютная система </b>- это организационно-экономическая форма отношений отдельной группы стран в валютной сфере, закрепленная региональными соглашениями.<br>

    Развитие валютных отношений предполагало создание не только мировых валютных систем, но и региональных. Наиболее совершенной из всех региональных валютных систем является
    Европейская валютная система (ЕВС), которая была сформирована в 1979 г. в рамках Европейского Экономического Сообщества с целью содействия развитию интеграционных процессов стран Западной
    Европы. Эту систему можно рассматривать как составную часть мировой валютной системы, поскольку страны, входящие в ЕВС, являются членами МВФ.<br>

    Денежной единицей ЕВС стала ЭКЮ (европейская валютная единица), которая выпускалась до 1994 г. Европейским фондом валютного сотрудничества (ЕФВС), а затем Европейским валютным институтом.
    Стоимость ЭКЮ определялась на базе «валютной корзины», в которую входили валюты 12 стран. Более 32% «корзины» приходилось на долю немецкой марки. ЭКЮ существовала только в форме записей
    на счетах центральных и коммерческих банков и безналичных перечислений по ним.<br>

    Более высокой ступенью интеграции и утверждения позиции Западной Европы в мире явился переход к использованию в наличной и безналичной формах обращения новой региональной валюты - евро.<br>

    Комиссией Европейского союза установлены жесткие требования к состоянию экономики стран - участниц ЕВС. В соответствии с этими требованиями с 1 января 1999 г. в И странах
    (Австрия, Бельгия, Германия, Ирландия, Испания, Италия, Люксембург, Нидерланды, Португалия, Финляндия, Франция) новая валюта была введена в безналичный оборот, заменив собой ЭКЮ.
    Россия не входит в ЕВС, так как ее экономическое положение пока не отвечает требованиям Европейского союза.<br>

   <hr>
    <b>Мировая валютная система сложилась </b>к середине XIX в. и является формой организации международных валютных отношений, закрепленной межгосударственными соглашениями.<br>

    Характер функционирования и стабильность мировой валютной системы зависят от степени соответствия ее принципов структуре мирового хозяйства, расстановке сил и интересам ведущих стран.
    При изменении данных условий возникает периодический кризис мировой валютной системы, который завершается ее крушением и созданием новой валютной системы.<br>

    Хотя мировая валютная система преследует глобальные мирохозяйственные цели и имеет особый механизм функционирования и регулирования, она тесно связана с национальными валютными системами.
    Эта связь осуществляется через национальные банки, обслуживающие внешнеэкономическую деятельность, и проявляется в межгосударственном валютном регулировании и координации валютной политики
    ведущих стран. Взаимная связь национальных и мировой валютных систем не означает их тождества, поскольку различны их задачи, условия функционирования и регулирования, влияние на экономику
    отдельных стран и мировое хозяйство.<br>

    Специфической особенностью развития мировых валютных отношений и мировой валютной системы является периодическое перемещение центров "валютной гегемонии", происходящих в силу неравномерности
    развития стран.<br>

    До первой мировой войны "валютная гегемония" закрепилась за Великобританией. Перемещение финансового центра из Европы в США в итоге первой мировой войны вызвало острую борьбу за валютную
    гегемонию между фунтом стерлингов и долларом. После второй мировой войны доллар стал единственной валютой-гегемоном.<br>

    В 70-90-е гг. кроме Западной Европе и США возникает новый центр – Япония, в результате чего ведущими мировыми валютами становятся: доллар США, немецкая марка, французский франк, японская
    йена, фунт стерлингов.<br>

    В настоящее время потенциальным новым центром "валютной гегемонии" может стать Китайская народная республика, доля которой в мирной торговле и производстве постоянно возрастает.

