    <b>2 этап</b> - затем текущая прибыль увеличивается на величину списанного износа основного капитала и нематериальных активов, на сумму отложенных налогов и др.<br>
    <b>На 3 этапе</b> определяется величина общих капитальных затрат необходимых для обеспечения производственной деятельности и поддержания текущего уровня прибыли предприятия.<br>
    <br>В результате получают чистые денежные потоки предприятия, которые используют при оценке его рыночной стоимости. Чистые денежные потоки представляют собой капитал фирмы,
