  <h3>
    <b>Государственный кредит</b> является важнейшим элементом государственных финансов.<br>
    Государственный кредит представляет собой отношения между государством в лице его органов власти и управления и юридическими и физическими лицами, при которых государство выступает
    в качестве заемщика, кредитора или гаранта.<br>
    Государственный кредит является формой дополнительной мобилизации государством финансовых ресурсов для финансирования общегосударственных потребностей и выполнения своих функций.<br>
    Государственный кредит используется, как правило, для покрытия дефицита бюджета, так как эмиссия денег, менее эффективная мера. С помощью займа регулируется необходимое количество денег
    в обращении.<br><br>

    <b>Функциями государственного кредита являются:</b><br>
    а) перераспределительная, которая осуществляет формирование централизованных денежных фондов государства и их использование на принципах срочности, плотности, возвратности; <br>
    Госзаймы помогают распределить средства в зависимости от текущих потребностей экономики страны. Деньги от кредитования могут направить на реализацию проектов социального значения или
    производственного сектора. <br>
    б) регулирующая. Регулирующую функцию реализовывают за счет определения процентных ставок. Когда правительство выходит на рынок заимствований она оказывает на него значительно влияние объемом проводимых сделок. Если государственный долг сокращается, процентные ставки снижаются. Продажа государственных ценных бумаг повышает ставки.<br>
    в) контрольная. обеспечивает своевременное погашение обязательств в полном объеме. Кроме того она обеспечивает эффективное и целевое использование заемных денег.<br><br>

    <b>Классификация государственных кредитов</b><br>
    Все государственный займы можно классифицировать не нескольким признакам:<br>
    - По эмитентам займы делятся на: размещаемые территориальными и центральными органами;<br>
    - По месту размещения: внутренние и внешние кредиты;<br>
    - По обращению на рынке: рыночные займы и нерыночные. Рыночные займы могут свободно продаваться и покупаться, они составляют основу финансирования дефицита бюджета. Нерыночные займы не могут свободно изменять своих владельцев, как правило, выпускаются государством для привлечения конкретных инвесторов;<br>
    - По cроку привлечения финансовых ресурсов: краткосрочные, среднесрочные, долгосрочные;<br>
    - По обеспеченности кредитных обязательств: закладные займы и беззакладные. Обеспечением закладных облигаций является конкретный залог, к примеру, определенное имущество. Подобные облигации в большинстве случаев выпускают местные органы власти. Центральными органами управления выпускаются беззакладные облигации, надежность которых исключительно высока, а инвесторы не требуют дополнительных гарантий;<br>
    - По типу выплачиваемого дохода займы могут быть: выигрышными, процентными, дисконтными;<br>
    - По методу определения дохода займы делятся на: с твердым доходом и с плавающим доходом.<br>
    <hr>
    <b>Управление государственным кредитом </b>— это совокупность действий государства, связанных с обслуживанием и погашением государственного долга, выпуском и размещением новых займов,
