  <h1>Вопрос:
    Эффект операционного левериджа.
    Цель и порядок расчета операционного левериджа.
    Назначение операционного анализа в финансовом менеджменте
  </h1>
  <hr>
  <h3>
    <b>Операционный рычаг (производственный леверидж) </b>- это потенциальная возможность влиять на прибыль компании, путем изменения структуры себестоимости и объем производства.<br>
    <b>Эффект операционного рычага </b>проявляется в том, что любое изменение выручки от продаж всегда ведет к более сильному изменению прибыли. Этот эффект вызван различной
    степенью влияния динамики переменных затрат и постоянных затрат на финансовый результат при изменении объема выпуска. Влияя на величину не только переменных, но и постоянных
    затрат, можно определить, на сколько процентных пунктов увеличится прибыль.
<hr>
    <b>Операционный леверидж</b> - это фактор изменения финансовых результатов, выражающийся в структуре текущих затрат и измеряемый, в частности, как доля материальных
    (нефинансовых) условно-постоянных затрат в общих затратах.<br>
    Операционный рычаг является инструментом для определения и анализа данной зависимости. Другими словами он предназначен для установления влияния прибыли на изменение объема
    реализации. Суть его действия заключается в том, что при росте объема выручки наблюдается больший темп роста объема прибыли, однако этот больший темп роста ограничен
    соотношением постоянных и переменных затрат. Чем ниже удельный вес постоянных затрат, тем меньше будет это ограничение.<br>
    Ценовой операционный рычаг вычисляется по формуле:<br>
    <b>Рц = В/П</b><br>
    Учитывая, что В = П + Зпер + Зпост, можно записать:

    Рц = (П + Зпер + Зпост)/П = 1 + Зпер/П + Зпост/П

    Натуральный операционный рычаг вычисляется по формуле:

    Рн = (В-Зпер)/П

    Учитывая, что В = П + Зпер + Зпост, можно записать:

    Рн = (П + Зпост)/П = 1 + Зпост/П

    Сравнивая формулы для операционного рычага в ценовом и натуральном выражении можно заметить, что Рн оказывает меньшее влияние. Это объясняется тем, что с увеличением натуральных объемов одновременно растут и переменные затраты, а с уменьшением — уменьшаются, что приводит к более медленному увеличению/уменьшению прибыли.

    Варьирование уровнем операционного левериджа означает большее или меньшее внимание к инвестициям в материально-техническую базу.

    Высокое значение операционного левериджа равносильно высокому производственному риску.

    Так, приобретение новой технологической линии сопровождается ростом уровня операционного левериджа и одновременно повышением общего риска, связанного с деятельностью данной фирмы, за счет появления риска неокупаемости этой линии.

    Высокий уровень операционного левериджа характерен высокотехнологичным отраслям, требующим по определению значительных инвестиций.


