  <h1>Вопрос:
    Страхование как финансовая категория.
    Специфика, сферы и отрасли страхования (пенсионное, медицинское и др.).
    Классификация форм страхования, их особенности
  </h1>
  <hr>
  <h3>
    <b>Страхование как финансовая категория </b>представляет собой совокупность перераспределительных отношений по защите интересов физических и юридических лиц за счет страхового (целевого)
    денежного фонда, формируемого из денежных взносов участников и используемого для возмещения ущерба при наступлении определенных событий.<br><br>
    Страхование представляет собой экономические отношения, возникающие в связи с формированием целевых фондов денежных средств, создаваемых для защиты имущественных интересов населения в
    частной и хозяйственной жизни от стихийных бедствий и других не предвиденных чрезвычайных событий, сопровождающихся ущербами.<br>

    Средства для страховых выплат формируются за счет уплачиваемых страхователями премий или взносов. Отсюда следует, что все затраты, связанные со страхованием, должны покрываться
    соответствующими взносами страхователей. Именно этот принцип финансирования является основой страхования.<br>

    С самого начала действия договора страхования страхователю гарантируется полное страховое покрытие. Сам факт уплаты страховых взносов дает ему право на страховую защиту в объеме, оговоренном
    в договоре. При этом не имеет значения, покрываются ли страховые выплаты по конкретному страховому случаю суммой уже накопленных по нему страховых взносов или нет.<br>

    <b>Страховая деятельность может осуществляться по двум направлениям: </b><br>
    1) как индивидуальное страхование. Оно является самостоятельной отраслью экономики и основывается на принципе эквивалентности страховых выплат и страховых взносов;<br>
    2) как социальное страхование. Оно является частью социальной политики государства и основывается на принципе коллективной солидарности. Социальное страхование направлено на ликвидацию
    социального неравенства в обществе и обеспечение минимального уровня благосостояния населения<br><br>

    <b>Отрасли, формы и виды страхования</b><br>

    Страхование представляет собой довольно разветвлённую систему отношений. Оно структурируется по областям, формам и видам. Отраслевая классификация осуществляется по объектам страхования.
    Выделения отдельных видов характеризует детализацию объектов страхования. Формы организации страхования указывают на их правовую основу:
    <b>обязательное или добровольное</b>.<br>
    Сочетание обязательного и добровольного страхования позволяет сформировать такую систему страхования, которая обеспечивает универсальный объем страховой защиты как общественного производства и общественных отношений, так и личных интересов граждан.
<br>
    <b>Классификация страхования:</b><br>

    <b>Области:</b><br>
    - имущественное (физических и юридических лиц);<br>
    - личное (жизнь и здоровья);<br>
    - социальное (трудоспособность и трудоустройство);<br>
    - медицинское;<br>
    - ответственности (на автотранспорте, профессиональной ответственности);<br>
    - рисков (предпринимательских и финансовых).<br>
    <b>Формы:</b><br>
    - обязательное;<br>
    - добровольное.<br>
    <b>Объектом имущественного страхования</b> является подвижное и неподвижное имущество юридических и физических лиц. Сегодня, например в РФ наиболее распространённые среди физических лиц
    страхования зданий, домашнего имущества, транспортных средств. Основная форма – добровольное страхование<br>
    <b>Объекты личного страхования</b> – это жизни и здоровья граждан. Видами личного страхования является, смешанное страхование жизни (объектами выступают одновременно жизнь и здоровье),
    страхование детей, свадебное страхование и т.п. <br>
    <b>В медицинском страховании </b>объектом страхования есть здоровье граждан. Оно осуществляется на случай болезни или травмы как в обязательном, так и дополнительно в добровольной форме через 
    государственные и негосударственные структуры. Страхователями могут быть: государство (из бюджета), предприятия, организации и учреждения и граждане (за счет собственных доходов).
    Страховое возмещение осуществляется в форме оплаты лечения.    

