  <hr>
  <h1>Вопрос:
    Внебюджетные фонды Российской Федерации.
    Задачи, особенности, источники образования внебюджетных фондов РФ.
    Источники формирования и направления использования средств внебюджетных фондов РФ.</h1>
  <hr>
  <h3>
    Впервые внебюджетные фонды появились в виде специальных фондов или особых счетов задолго до возникновения единого центрального денежного фонда государства — бюджета.
    С расширением своих функций государство нуждалось во все новых расходах, требовавших средств для своего покрытия. Эти средства концентрировались в особых фондах,
    предназначенных для специальных целей. Такие фонды носили, как правило, временный характер. С выполнением государством намеченных мероприятий они заканчивали свое
    существование. В связи с этим количество фондов постоянно менялось: одни возникали, другие аннулировались.<br>
    <b>Согласно ст. 143 БК РФг осударственный внебюджетный фонд</b> – фонд денежных средств, образуемый вне федерального бюджета и бюджетов субъектов РФ и предназначенный для 
    реализации конституционных прав граждан на пенсионное обеспечение, социальное страхование, социальное обеспечение в случае безработицы, охрану здоровья и медицинскую 
    помощь. Расходы и доходы государственного внебюджетного фонда формируются в порядке, установленном федеральным законом, либо в ином порядке, предусмотренном БК РФ.<br>
    <b>Государственными внебюджетными фондами в РФ являются (ст. 144 БК РФ):</b><br>
    1) Пенсионный фонд РФ;<br>
    2) Фонд социального страхования РФ;<br>
    3) Федеральный фонд обязательного медицинского страхования;<br>
    4) Государственный фонд занятости населения РФ. <br>
<hr>
    <b>Основные задачи внебюджетных фондов:</b><br>
    1) влиять на процесс производства путем финансирования, субсидирования, кредитования отечественных предприятий;<br>
    2) обеспечивать природоохранительные мероприятия, финансируя их за счет специально определенных источников и штрафов за загрязнение окружающей среды;<br>
    3) оказывать социальные услуги населению путем выплаты пенсий, пособий, субсидирования и финансирования социальной инфраструктуры;<br>
    4) предоставлять займы, в том числе зарубежным партнерам.<br>
    <b>С помощью внебюджетных фондов возможно:</b><br>
    • влиять на процесс производства путем финансирования, субсидирования, кредитования отечественных предприятий;<br>
    • оказывать социальные услуги населению путем выплаты пособий, пенсий, субсидирования и финансирования социальной сферы;<br>
    • обеспечивать природоохранные мероприятия, финансируя их за счет специально определенных источников и штрафов за загрязнение окружающей среды;<br>
    • обеспечивать инфраструктуру в целом;<br>
    • предоставлять займы, в том числе зарубежным партнерам, включая иностранные государства.    <br><br>
    Внебюджетные фонды создаются двумя путями. <br>
    Один путь - это выделение из бюджета определенных расходов, имеющих особо важное значение, <br>
    другой - формирование внебюджетного  фонда с собственными источниками доходов для определенных целей.<br>
    Так, во многих странах был создан фонд социального страхования, предназначенный для социальной поддержки
    определенных групп населения. Другие фонды появляются в связи с возникновением новых ранее неизвестных расходов, которые заслуживают особого внимания со стороны общества.
    В этом случае по предложению правительства законодательный орган принимает специальное решение об образовании данного внебюджетного фонда.<br>
    Материальным источником внебюджетных фондов является национальный доход.<br>
    Преобладающая часть фондов создается в процессе перераспределения национального дохода. Основные методы мобилизации национального дохода
    в процессе его перераспределения при формировании фондов - специальные налоги и сборы, средства из бюджета и займы.
    <hr>
    Федеральные внебюджетные фонды формируются за счет следующих источников:<br>
    - специальные целевые налоги и сборы, установленные для соответствующего фонда;<br>
    - отчисления от прибыли предприятий, учреждений и организаций;<br>
    - средства бюджета;<br>
    - прибыль от коммерческой деятельности, осуществляемой фондом как юридическим лицом;<br>
    - займы, полученные фондом у Центрального банка РФ или у коммерческих банков.  <br>
    Направления расходования средств, поступающих во внебюджетные фонды, обусловливается назначением фондов. Помимо целевого использования средств этих фондов
    (при этом на конкретную цель указывает само название фонда: Пенсионный фонд РФ создан для финансирования расходов по государственному пенсионному обеспечению
    граждан, фонды обязательного медицинского страхования — для финансирования гарантированной государством медицинской помощи гражданам и т. д.) они могут направляться на
    содержание органов управления фондом, на инвестиционную деятельность и т.п. <br>
    <b>Например</b>:
    Фонд государственного социального страхования – это централизованный фонд денежных ресурсов общегосударственного назначения, распределяемый как в территориальном,
    так и в отраслевом разрезах.  Данный фонд создается страховым методом с обязательным участием предприятий и лиц, занимающихся предпринимательской деятельностью.
<br>
    <b>Источниками образования фонда являются</b>:<br>
    - отчисления от обязательных платежей по единому социальному налогу; <br>
    - доходы от инвестирования временно свободных денежных средств ФСС и их капитализации; <br>
    - ассигнования из бюджета; <br>
    - добровольные взносы граждан; <br>
    - другие поступления.<br>

    <b>Средства Фонда социального страхования направляются на:</b><br>
    1) выплату пособий по временной нетрудоспособности, беременности и родам, при рождении ребенка, по уходу за ребенком до достижения им возраста полутора лет, на погребение;<br>
    2) санаторно-курортное лечение и оздоровление работников и членов их семей, а также на другие цели государственного страхования, предусмотренные законодательством (частичное содержание санаториев-профилакториев, санаторных и оздоровительных лагерей для детей и юношества, лечебное питание, оплату проезда к месту лечения и отдыха и обратно и др.);<br>
    3) обеспечение гарантированных государством пособий по временной нетрудоспособности, беременности и родам, при рождении ребенка, по уходу за ребенком до достижения им возраста полутора лет, на погребение, санаторно-курортное лечение и оздоровление работников и членов их семей, а также другие цели государственного социального страхования;<br>

