    Все безналичные расчеты осуществляются на основании платежных документов, имеющих хождение только во внутрибанковском обороте.<br>

    Операции по безналичным расчетам отражаются на расчетных, текущих и иных счетах, открываемых банками своим клиентам после предоставления последними соответствующих документов.
    <hr>
    <b>Система безналичных расчетов, ее основные элементы</b><br>
    Главная составляющая денежного оборота - безналичный оборот. На его долю приходится до 90% денежного оборота страны. При этом денежные расчеты между поставщиком и покупателем товаров,
