  <h1>Вопрос:
    Фонд государственного социального страхования.
    Понятие фонда государственного социального страхования.
    Задачи, источники формирования, направления расходования средств фонда государственного социального страхования.</h1>
  <hr>
  <h3>
    В июле 2022 года Госдумой было принято решение о слиянии к 1 января 2023 года ФСС и Пенсионного фонда России (ПФР) с созданием единого Фонда пенсионного и социального
    страхования Российской Федерации (кратко: Социального фонда России, СФР).<br><br>

    Социальное страхование — это система, по которой государство защищает людей, временно оставшихся без дохода по каким-то причинам — например, из-за болезни.
    Фонд социального страхования — ФСС — существует, чтобы государство могло контролировать расходы на социальное страхование граждан.<br><br>

    <b>Фонд социального страхования</b> — это государственный фонд, который работает согласно федеральным законам, указам президента и постановлениям правительства, а его бюджет и
    имущество принадлежат государству.  При этом фонд — внебюджетный, то есть его деньги не учитываются в федеральном бюджете. Это значит, что государство не может взять деньги из ФСС и
    направить, например, на реализацию программы поддержки малого бизнеса.<br><br>

    Суть фонда в том, что в нем копятся страховые взносы от работодателей за их работников и от всех, кто страхует себя добровольно. Эти деньги фонд тратит на возмещение заработка тем,
    кто попал в ситуацию временной нетрудоспособности, то есть не может работать из-за болезни, несчастного случая на производстве или беременности и родов. Поясним на примере.<br>
    <b>Пример</b><br>
    Кирилл приходил на работу во все рабочие дни, исправно выполнял свои обязанности и получал зарплату. В один день он на работу не вышел — заболел гриппом, открыл больничный лист и
    остался лечиться дома. Работодатель не будет платить Кириллу зарплату, ведь тот дома, лечится и свои трудовые функции не исполняет. Но Кирилл без денег не останется: его застраховали на
    такие случаи, поэтому он получит пособие, которое компенсирует отсутствие зарплаты из-за болезни. За первые 3 дня заплатит работодатель, за остальные дни болезни — ФСС.<br>
    На месте Кирилла может быть любой человек, работающий по трудовому договору или застраховавшийся добровольно.
<hr>
    <b>Основными задачами Фонда являются:</b><br>
    - обеспечение гарантированных государством пособий: по временной нетрудоспособности, беременности и родам, при рождении ребенка, по уходу за ребенком до достижения им возраста полутора лет,
    на погребение, санаторно-курортное лечение и оздоровление работников и членов их семей;<br>
    - участие в разработке и реализации государственных программ охраны здоровья работников, мер по совершенствованию социального страхования;<br>
    - осуществление мер для обеспечения финансовой устойчивости Фонда, в том числе создание резерва;<br>
    - частичное содержание санаториев-профилакториев, санаторных и оздоровительных лагерей для детей и юношества, лечебное питание, частичное финансирование мероприятий по внешкольному
    обслуживанию детей, оплата проезда к месту лечения и отдыха и обратно;<br>
    - разработка совместно с Министерством труда и социального развития РФ предложений о размерах тарифа страховых взносов на государственное социальное страхование;<br>
    - организация разъяснительной работы среди страхователей и населения по вопросам социального страхования.<br>

    <b>Средства ФСС образуется за счет:</b><br>
    - страховых взносов работодателей; страховых взносов граждан, занимающихся индивидуальной трудовой деятельностью, а также осуществляющих трудовую деятельность на иных условиях и имеющих право на обеспечение по государственному
    социальному страхованию (примерно 92% от общей суммы доходов);<br>

    - доходов от инвестирования части временно свободных средств фонда в ликвидные государственные ценные бумаги и банковские вклады в пределах средств, предусмотренных бюджетом на
    соответствующий период;<br>

    - добровольных взносов физических и юридических лиц;<br>

    - ассигнований из республиканского бюджета РФ на покрытие расходов, связанных с доставлением льгот и компенсаций лицам, пострадавшим вследствие катастрофы на Чернобыльской АЭС
    или радиационных аварий на других атомных объектах гражданского или военного назначения и их последствий;<br>

    - прочих доходов (возмещаемые страхователем расходы, не принятые к зачету в счет страховых взносов, не принятые к зачету расходы на выплату пособий по временной нетрудоспособности
    вследствие трудового увечья или профессионального заболевания; недоимки по обязательным платежам, суммы штрафов и иных санкций, предусмотренные законодательством; уплаченные в
    установленном порядке суммы за путевки, приобретенные страхователем за счет средств Фонда, возмещаемые Фонду в результате исполнения регрессных требований к страхователям и др.).<br><br>

    большая часть средств социального страхования поступает работникам непосредственно от работодателей, а меньшая (примерно 1/4) перечисляется в Фонд социального страхования для
    перераспределения между отдельными предприятиями, организациями, учреждениями. Из нее видно также и то, что часть поступлений в ФСС осуществляется из федерального бюджета.
    Это компенсационные поступления по расходам на выплату пособий, санаторно-курортное лечение и оздоровление в соответствии с Законом РФ «О социальной защите граждан, подвергшихся
    воздействию радиации вследствие катастрофы на Чернобыльской АЭС».<br><br>

    <b>Средства фонда направляются на:</b><br>

    - выплату пособий по временной нетрудоспособности, беременности и родам женщинам, вставшим на учет в ранние сроки беременности, при рождении ребенка, по уходу за ребенком до достижения им возраста 1,5 лет, а также социального пособия на погребение;
<br>
    - оплату дополнительных выходных дней по уходу за ребенком-инвалидом или инвалидом с детства до достижения им возраста 18 лет;
<br>
    - санаторно-курортное лечение и оздоровление работников и членов их семей, в т.ч. расходы на лечебное питание;
<br>
    - частичное содержание находящихся на балансе страхователей санаториев-профилакториев, имеющих лицензии на право занятия этим видом деятельности (оплата расходов на питание, лечение и медикаменты, заработную плату работников, культурно-массовое обслуживание);
<br>
    - частичную оплату путевок в детские загородные оздоровительные лагеря, находящиеся на территории РФ, для детей работающих граждан;
<br>
    - частичное содержание детско-юношеских спортивных школ;
<br>
    - оплату проезда к месту лечения и обратно;
<br>
    - создание резерва для обеспечения финансовой устойчивости фонда на всех уровнях;
<br>
    - обеспечение текущей деятельности, содержание аппарата управления фонда;
<br>
    - проведение НИР по вопросам социального страхования и охраны труда;
<br>
    - осуществление иных мероприятий в соответствии с задачами фонда.

