  <h1>Вопрос:
    Фонд обязательного медицинского страхования РФ.
    Задачи, источники формирования, направления расходования средств фонда обязательного медицинского страхования РФ.</h1>
  <hr>
  <h3>
    <b>Медицинское страхование</b> – это одна из форм социальной защиты населения в случае потери здоровья по любой причине.<br>

    Цель медицинского страхования гарантировать гражданам при возникновении страхового случая (заболевания) получение медицинской помощи за счёт накопленных средств и финансирования
    профилактических мероприятий.<br>

    Закон РФ от 28 июня 1991 г. “О медицинском страховании граждан” был полностью введён в действие с 18 января 1993 года. Введение обязательного медицинского страхования означало для
    государственного здравоохранения переход к смешанной системе финансирования, а именно – к бюджетно-страховой системе.<br>

    Бюджетные средства обеспечивают финансирования в части неработающего населения (пенсионеры, домохозяйки, учащиеся) а внебюджетные средства – работающих граждан.<br>

    Страхователями выступают исполнительные органы субъектов РФ, местное самоуправление и хозяйствующие субъекты а так же граждане – предприниматели.<br>

    Для реализации указанного закона и осуществление государственной политик в области обязательного мед. страхования были сформированы федеральный и территориальные фонды обязательного
    мед. страхования. Фонды ОМС являются самостоятельными некоммерческими финансовыми учреждений. Они предназначены для аккумулирования финансовых ресурсов на обязательное мед. страхования,
    обеспечение финансовой стабильности государственной системы ОМС и выравнивание фин. Ресурсов на его проведение.<br><br>

    Федеральный фонд ОМС создан с постановления верховного совета РФ от 24 января 1993. На федеральный фонд возложены<br>
    <b>следующие функции</b>:<br>

    1. Создание условий деятельности территориальных фондов ОМС по обеспечению финансирования программ обязательного мед. страхования.<br>
    2. Финансирование целевых программ в рамках обязательного мед. страхования.<br>
    3. Разработка нормативно методических документов, обеспечивающих реализацию закона “о мед. страховании граждан РФ”<br>
    4. Организация подготовки специалистов ОМС<br>
    5. Участие в создании территориальных фондов ОМС и в разработке программ обязательного медицинского страхования.<br>

    <b>Финансовые средства фонда формируются за счёт:</b><br>
    1. Страховых взносов работодателей в виде обязательных отчислений.<br>
    2. Взносов территориальных фондов ОМС на реализацию совместных программ, выполняемых на договорных началах.<br>
    3. За счёт ассигнований из федерального бюджета на выполнение республиканских программ ОМС.<br>
    4. За счёт доходов от использования временного свободных финансовых средств федерального фонда.<br>
    5. За счёт нормированного страхового запаса финансовых средств фонда.<br>
    6. За счёт добровольных взносов и иных поступлений не запрещённых законодательством РФ.    <br><br>

    Территориальные фонды ОМС создаются местными органами власти и действуют на основании положения о территориальном фонде ОМС. Который утверждён постановлением верховного совета 24 февраля 1993г.
    В регионах (областях, краях, республиках) могут создаваться филиалы. Территориальный фонд создаётся для финансирования территориальных программ ОМС. <br>
    <b>Территориальный фонда ОМС выполняет следующие функции:</b><br>
    - Аккумулирует финансовые средства территориального фонда на ОМС.<br>
    - Осуществляет финансирование территориальной программы ОМС.<br>
    - Осуществляет финансово кредитную деятельность по обеспечению деятельности системы ОМС.<br>
    - Выравнивает финансовые ресурсы городов, районов и др. территорий.<br>
    - Осуществляет контроль за своевременным и полным поступлением в территориальный фонд страховых взносов.<br>
    - Формирует банк данных по всем категориям плательщиков страховых взносов в территориальный фонд.<br>
    - Осуществляет взаимодействие с федеральным фондом и другими территориальные фондами.<br>
    Главными доходами ОМС являются страховые взносы работодателей и страховые платежей на неработающее население (90% от общей суммы доходов)<br>
    Порядок зачисления сумм страховых взносов определяется федеральным казначейством и министерство финансов. Что касается страховых взносов по неработающему населению, то их регламентирует
    правительство субъектов федерации и местная администрация за счёт средств предусмотренных в бюджетах.

  </h3>
