  <hr>
  <h1>Вопрос:
    Математические основы финансового менеджмента.
    Отличительные особенности дохода и доходности в финансовом менеджменте.
    Показатели эффективности деятельности предприятия, применяемые в финансовом менеджменте.</h1>
  <hr>
  <h3>
    В основе финансовых вычислений лежит понятие временной ценности денег, которое может быть выражено следующим выражением: «рубль сегодня более ценен, чем в будущем». Данное заключение определяется рядом факторов:
<br>
    <b>во-первых</b>, денежная единица, находящаяся в распоряжении лица может быть использована как в целях потребления, так и в целях преумножения, тогда как денежная единица, которую ожидается получить в будущем лишена возможности получить дополнительный доход сегодня, а также обладает риском неполучения.
<br>
    <b>во-вторых</b>, в условиях инфляционной экономики, деньги имеют объективную особенность обесценения и потери покупательной способности, из чего следует, что на одну и ту же денежную единицу в условиях инфляции сегодня можно купить больше, чем в будущем.
<br>
    В финансовых расчетах временная несопоставимость и плата за отказ от потребления учитываются с помощью операций наращения и дисконтирования.
<br>
    Одним из важнейших определений, лежащих в основе большинства финансовых расчетов, являются <b>процентные деньги</b><br>
    <b>Проценты </b> – это доход от предоставления капитала в долг в различных формах (ссуды, кредиты и т.д.), либо от инвестиций производственного или финансового характера.
<br>
    <b>Процентная ставка </b>– это величина, характеризующая интенсивность начисления процентов.
<br>
    Величина получаемого дохода (процентов) определяется исходя из величины вкладываемого капитала, срока, на который он предоставляется в долг или инвестируется, размера и вида процентной ставки (ставки доходности).
<br>
    <b>Капитализация (наращение) долга </b>– это увеличение суммы долга за счет присоединения начисленных процентов (дохода).
<br>
    <b>Коэффициент капитализации (наращения) </b>– величина, показывающая, во сколько раз вырос первоначальный капитал.
<br>
    <b>Период начисления </b>– период времени, в течение которого начисляются проценты. Период начисления, как правило, совпадает со сроком кредита. Период начисления может разбиваться на интервалы начисления.
<br>
    <b>Интервал начисления </b>– это минимальный период, по прошествии которого происходит начисление процентов.
<br>
    Существуют два способа начисления процентов:
<br>
    <b>1. Декурсивный способ начисления процентов</b>. Декурсивные проценты (ссудный процент) начисляются в конце каждого интервала начисления исходя из величины предоставляемого капитала.
<br>
    <b>2. Антисипативный (предварительный) способ начисления процентов</b>. Антисипативные проценты (учетная ставка) начисляются в начале каждого интервала начисления исходя из наращенной суммы.
<br>
    При обоих способах начисления процентов процентные ставки могут быть либо простыми, если применяются в течение всего периода начисления к первоначальной денежной сумме, либо сложными, если по прошествии каждого интервала начисления применяются к наращенной сумме.
<br><br>
    <b>Простые ставки ссудных процентов</b> обычно применяются в краткосрочных финансовых операциях (не более года), когда интервал начисления совпадает с периодом начисления, либо когда после каждого интервала начисления кредитору выплачиваются проценты.Сложные ставкиссудных процентов применяются как правило в длительных финансовых операциях (более года), когда подразумевается наращение основной суммы.
    <br><br>
    В зависимости от способа определения продолжительности финансовой операции рассчитывается либо точный, либо обыкновенный (коммерческий) процент. В первом случае используется точное число дней ссуды. Во втором случае берется приблизительное число дней ссуды, когда продолжительность месяца принимается равной 30 дней, а продолжительность года принимается 360 дней. Дата выдачи и гашения кредита всегда считается за один день.
<hr>
    <br><b>Доход в финансовом менеджменте</b> – это положительный результат деятельности собственников, выраженный показателем прибыли, остающейся в распоряжении предприятия и измеряемый в денежном выражении.
    <br><b>Доходность</b> – это относительный показатель, характеризующий эффективность вложения денежных средств, определяемый как отношение результата (дохода) и затрат, необходимых для достижения данного результата и измеряемый в процентах или долях единицы.
<hr>
    В российской практике в качестве основных критериев оценки эффективности финансово-хозяйственной деятельности предприятий используют следующие параметры:<br>
    - выручку от реализации продукции, работ, услуг (объем продаж);<br>
    - бухгалтерскую и чистую прибыль, остающуюся после налогообложения;<br>
    - рентабельность издержек, активов (имущества), инвестиций, объема продаж и т. д.;<br>
    - финансовую стабильность;<br>
    - финансовый результат собственников предприятия.<br>

    С целью оценки эффективности работы предприятий обычно используют показатели финансовой устойчивости, доходности, платежеспособности и деловой активности.<br>
    <b>Финансовая устойчивость</b> означает такое финансовое состояние предприятия, которое обеспечивает не только стабильное превышение его доходов над расходами, но рост прибыли при сохранении эффективного и бесперебойного функционирования хозяйствующего субъекта.
    <br>
    <b>Рентабельность</b> – один из основных качественных показателей эффективности производства на предприятии, характеризующий уровень отдачи затрат и степень использования
    средств в процессе производства и реализации продукции.<br>
    <b>Деловая активность</b> характеризует эффективность текущей деятельности предприятия и связана с результативностью использования материальных, трудовых, финансовых ресурсов предприятия и с показателями оборачиваемости капитала.<br>
    <b>Ликвидность</b> – способность покрывать свои обязательства активами, срок превращения которых в денежную форму соответствует сроку погашения обязательств.
    Ликвидность означает безусловную платежеспособность организации и предполагает постоянное равенство между ее активами и обязательствами.
  </h3>
