  <h1>Вопрос:
    Участники кредитных отношений.
    Формы и виды кредита.
    Формы обеспечения кредитования юридических и физических лиц.
  </h1>
  <hr>
  <h3>
    Кредит состоит из элементов, находящихся в тесном взаимодействии друг с другом. Такими элементами являются субъекты его отношений. В кредитной сделке субъекты отношений всегда выступают
    как <B>кредитор</B> и <B>заемщик</B>.<br>

    <B>Кредитор</B> – сторона кредитных отношений, предоставляющая ссуду. Кредиторами могут стать субъекты, реально предоставляющие нечто во временное пользование. Источниками ссуды могут стать
    как собственные накопления, так и ресурсы, позаимствованные у других субъектов. С образованием банков происходит концентрация кредиторов. Мобилизуя временно свободные денежные ресурсы
    предприятий и населения, банкиры становятся коллективными кредиторами.<br>

    Положение кредитора по отношению к ссужаемым средствам двоякое. Собственные ресурсы кредитора, передаваемые на основе кредита, остаются его собственностью. Собственником привлеченных
    средств, размещаемых кредитором, остаются предприятия и население. Размещая ссуженную стоимость, кредитор обеспечивает ее производительное использование как для своих собственных целей,
    так и для целей других участников воспроизводственного процесса.<br>

    <B>Заемщик</B> – сторона кредитных отношений, получающая кредит и обязанная возвратить полученную ссуду. <B>Должник
    и заемщик</B> – близкие, но не одинаковые понятия. Долг – это состояние не
    только экономических, но и чисто человеческих отношений; долг – это более широкое понятие, характеризующее обязанность вообще. Применительно к кредитной сделке речь идет не о должнике,
    а о заемщике.<br>

    С образованием банков происходит концентрация не только кредиторов, но и значительное расширение состава заемщиков. В современных условиях помимо банков заемщиками выступают предприятия,
    население и государство. При этом банки становятся коллективными заемщиками, поскольку занимают не для себя, а для других.<br>

    Особое место <B>заемщика</B> в кредитной сделке отличает его от кредитора.<br>

    <B>Во-первых</B>, заемщик не является собственником ссужаемых средств, он выступает лишь временным владельцем, пользуется чужими средствами.<br>

    <B>Во-вторых</B>, заемщик применяет ссужаемые средства как в сфере обращения, так и в сфере производства. Кредитор же предоставляет ссуду в фазе обмена, не входя непосредственно в производство.<br>

    <B>В-третьих</B>, заемщик возвращает ссужаемые ресурсы, завершившие кругооборот. Для обеспечения такого возврата заемщик должен так организовать свою деятельность, чтобы обеспечить высвобождение
    средств, достаточных для расчетов с кредитором.<br>

    <B>В-четвертых</B>, заемщик не только возвращает стоимость, полученную во временное пользование, но и уплачивает при этом ссудный процент.<br>

    <B>В-пятых</B>, заемщик зависит от кредитора, кредитор диктует свою волю. Экономическая зависимость от кредитора заставляет заемщика рационально использовать ссуженные средства, выполнять свои
    обязательства как ссудополучателя.<br>

    Занимая зависимое от кредитора положение, <B>заемщик</B> не теряет своей значимости в кредитной сделке как полноправной стороны. Без заемщика не может быть кредитора. Заемщик не только получает,
    но и использует полученные ссужаемые ресурсы, причем так, чтобы полностью рассчитываться по своим долгам. В этом смысле заемщик – производительная сила, от которой зависит эффективное
    применение ресурсов, полученных во временное пользование.<br>

    Вступая в кредитные отношения, кредитор и заемщик демонстрируют единство своих целей и интересов. Отношения между кредитором и заемщиком, их связь друг с другом представляют собой отношения
    двух субъектов, выступающих, во-первых, как юридически самостоятельные лица, во-вторых, как участники кредитных отношений, обеспечивающие имущественную ответственность друг перед другом,
    в-третьих, как субъекты, проявляющие взаимный экономический интерес друг к другу.

