    <hr>
    <B>Формы и виды кредита</B>
    Для привлечения клиентов банки предлагают различные виды кредитных продуктов. Они делятся по условиям и целям.<br>

    По целям кредитования.<br>
    В зависимости от целей кредит может быть <B>целевым</B> и <B>нецелевым</B>:<br>

    <B>Целевой</B> – берется на удовлетворение определенной потребности – приобретение недвижимости, автомобиля, оплату образования.<br>
    <B>Нецелевой</B> – заемщик не отчитывается перед банком, на что он потратит средства. Обычно процентная ставка по таким нецелевым кредитам ощутимо выше.<br>
    По виду обеспечения.
    <B>Необеспеченные</B> – клиент не предоставляет дополнительных гарантий возврата средств, поэтому он может рассчитывать на небольшую сумму, выданную на короткий срок. Ставка выше, чем у кредитов
    с обеспечением.<br>
    <B>Обеспеченные залогом </B>– в качестве залога может выступать недвижимое имущество, транспорт, страховка, ценные бумаги.<br>
    <b>Обеспеченные поручительством</b> – поручителем могут быть физические и юридические лица. Если заемщик перестанет выполнять свои обязательства, то они перейдут к его поручителю.<br><br>
    <b>Потребительский кредит</b><br>
    Потребительским он называется потому, что выдается для целей потребления – к примеру, можно купить телефон или бытовую технику, отправиться в отпуск, сделать ремонт, оплатить дорогостоящее
    лечение, «пережить» период до зарплаты и так далее.<br>

    Доступен для физических лиц. Возврат заемщика – от 18 лет. Для зарплатных клиентов банка действуют особо выгодные условия – ниже проценты и больше срок кредитования. Некоторые банки предлагают
    снизить процентную ставку с помощью подключения дополнительных услуг. Максимальный срок кредитования, как правило, составляет 5-7 лет.<br>

    <b>Ипотека</b> – это самый простой для обычных людей способ купить жилье в России. Юридические лица тоже могут оформить ипотечный кредит на приобретение коммерческой недвижимости – офисов,
    складов, цехов, торговых помещений и других объектов.<br>

    Данный вид кредитования регулируется отдельным нормативно-правовым актом, а именно Федеральным законом «Об ипотеке (залоге недвижимости)» от 16.07.1998 № 102-ФЗ. Это –
    <b>целевой кредит</b>, он выдается под залог приобретаемой, либо уже имеющейся у заемщика недвижимости (это может быть квартира, дача, земельный участок). Недвижимым имуществом можно
    пользоваться, но с небольшими ограничениями: оно не может выступать объектом сделки купли-продажи, обмена и дарения. Обременение снимается сразу же после погашения ипотеки.<br>

    Условия по ипотечному кредитованию значительно отличаются от потребительского кредита.<br>
    <b>Во-первых</b>, почти всегда требуется первоначальный взнос (примерно 15-20%) от стоимости жилья. <br>
    <b>Во-вторых</b>, заемщик может рассчитывать на крупную сумму (до 30 миллионов рублей). <br>
    <b>В-третьих</b>, срок кредитования может достигать до 30 лет.<br>

    Также государством предусмотрены различные программы получения ипотеки на льготных условиях – для военных, для семей с детьми, на покупку недвижимости в сельской местности, для молодых
    семей, для бюджетных работников.<br>

    <b>Автокредит</b><br>
    Это кредит под залог приобретаемого автомобиля. Суть кредитования аналогична ипотечному – залоговое имущество нельзя продать, обменять, подарить. Кроме того, покупатель обязательно
    должен застраховать машину по ОСАГО и по КАСКО. Получить автокредит можно практически в любом автосалоне – банки с ними тесно сотрудничают. Кроме того, с помощью автокредита можно купить
    и подержанный автомобиль с пробегом.<br>

    Автокредиты бывают стандартные и коммерческие (для организаций), предусматривающие покупку спецтехники, грузового транспорта, автобусов для использования на производстве. Условия выдачи:
    срок обычно до 5 лет, сумма – до 7 миллионов рублей.<br>

    <b>Кредитная карта</b><br>
    <b>Кредитка</b> – это тоже вид банковского кредитования. Представляет собой обычную пластиковую карточку, на которой лежат деньги банка. Ими можно распоряжаться по своему усмотрению,
    оплачивать любые покупки в офлайн-магазинах и в интернете. Главное – не превысить кредитный лимит. Он определяется индивидуально для каждого клиента и зависит от кредитной истории,
    дохода, тарифа карты.<br>

    Заемщику предоставляется льготный период (в среднем от 1 до 3 месяцев), в течение которого он может вернуть деньги без переплаты. Обслуживание кредитки может быть и платное, и бесплатное
    (если тратить с карточки определенную сумму в месяц).<br>

    Многие банки предлагают дополнительные «плюшки» для владельцев кредиток: повышенный кэшбэк в магазинах-партнерах, бесплатные подписки на развлекательные сервисы, благотворительные акции.

    <!--    -->
    <hr>
    <b>Заемщик может обеспечить кредит по-разному:</b><br>

    - поручительством;
    - залогом — это может быть ликвидное имущество или активы заемщика, которые можно легко и быстро продать;
    - страховкой.<br>
    Любое из обеспечений гарантирует банку, что выданные средства будут возвращены. Иногда кредиторы требуют оформления сразу нескольких форм гарантий для одного займа.
    Такое возможно, если потенциальный заемщик просит слишком крупную сумму или у него возникали проблемы в кредитной истории.<br>

    <b>Поручительство</b> — одна из самых распространенных форм обеспечения кредита. Такой тип гарантий часто встречается при оформлении займа на крупную сумму.<br>
    Поручителем по кредиту могут стать:<br>

    - супруг или супруга заемщика, которые не выступают созаемщиками по этому же договору;<br>
    - другие члены семьи, если банк признал, что у них все в порядке с платежеспособностью;<br>
    - сторонние лица, которые готовы выступить гарантом по возврату долга основным заемщиком, например, коллеги, друзья, соседи;<br>
    - юридические лица и индивидуальные предприниматели, в том числе работодатели заемщика.<br>

    Сотрудники банка проверяют каждого поручителя на платежеспособность так же тщательно, как и основного заемщика. При просрочке платежей и отказе возвращать заемную сумму должником,
    кредитор потребует возврат денег с гаранта в полном или частичном объеме в зависимости от прописанных в договоре условий.<br>

    Банк может потребовать не только основную сумму долга, но и штрафы, пени, которые успели набежать за период просрочки. А также судебные издержки, если дело рассматривалось в суде.<br>

    Поручительство прекращается, если:<br>

    - истек срок договора поручительства;<br>
    - заемщик исполнил обязательства перед банком;<br>
    - произошла смена поручителя, что подтверждено дополнительным соглашением;<br>
    - произошла смена заемщика, а поручитель отказался выступать его гарантом;<br>
    - изменены условия договора кредитования, а поручитель с ними не согласен, поэтому не подписал дополнительное соглашение к первоначальному договору.<br>

    Если банк меняет условия кредитного договора, например, увеличивает срок выплаты или процентную ставку, сотрудники кредитора уведомляют и заемщика, и поручителя. Изменения фиксируют в
    дополнительном соглашении, однако без подписи гаранта и должника они не вступят в силу.<br><br>

    <b>Страховой полис</b> — самая распространенная форма обеспечения кредита. Гарантом возврата заемных денег выступает страховая компания, к которой переходят обязательства заемщика при
    наступлении определенных условий. Договор вступает в действие в день подписания.<br>

    В большинстве банков оформление страховки — обязательное условие при получении кредита. Так кредитор снижает риск невозврата выданной суммы и получает дополнительную гарантию от страховщика.<br>
    При оформлении страховки сумма долга для заемщика становится больше, а такие условия подходят не всем. Но выплачивать страховку можно не сразу: иногда ее делят на пропорциональные части
    и добавляют к ежемесячным платежам по кредиту.<br>

    При оформлении кредитного договора используют два типа страхования рисков:<br>

    - страхование заемщика на случай смерти, болезни или увольнения с работы;<br>
    - страхование имущества, которое выступает залогом по кредиту.<br>
    Если страхование залогового имущества — это обязательная мера, то личное страхование — добровольная. Кредитор не может принудить заемщика оформлять оба вида страховки, также как и заставить
    выбрать конкретную страховую компанию. Если клиент найдет подходящую страховую компанию сам, то это позволит ему сэкономить средства, так как условия стороннего страховщика могут оказаться
    более выгодными. Однако банк должен одобрить выбор заемщика, иначе кредитор может отказать в оформлении договора займа.<br><br>

    <b>Обеспечение депозитом</b>
    Депозит в качестве обеспечения кредита встречается редко. К такой форме гарантии прибегают заемщики, у которых в том же банке, где берется кредит, уже оформлен банковский вклад.<br><br>

    <b>Залог</b><br>
    Залогом по кредиту может стать:<br>

    - движимое и недвижимое имущество,<br>
    - право на собственность,<br>
    - товары в обороте компании,<br>
    - ценные бумаги и другие активы.<br>
    Банки не хотят рисковать, поэтому всегда придерживаются правила: стоимость залога выше, чем сумма кредита. Это связано с тем, что со временем имущество может подешеветь, а на продажу актива
    кредитор тратит время и деньги.  <br><br>

    <b>Залогом при кредитовании чаще всего становятся такие виды имущества:</b>

    - квартиры, дачи, дома, магазины, гаражи, офисы, другие жилые и нежилые помещения;<br>
    - машины, лодки и другие виды транспорта;<br>
    - земельные участки с постройками и без;<br>
    - недостроенные и не введенные в эксплуатацию здания;<br>
    - другое ликвидное имущество, например, бытовая техника, компьютеры, драгоценности.<br>

    Перед заключением кредитного договора банк оценит залог. Обычно сумма кредита это 50-70% от стоимости имущества. Такой процент покроет риски банка. Кредиторы всегда отдают предпочтение
    активам, которые можно быстро реализовать в случае невозврата заемных денег.<br><br>

    <b>Залог — предмет покупки</b><br>
    Залогом может стать предмет покупки, на которую заемщик берет деньги — квартира, машина, дом, участок земли. После сделки и на срок погашения кредита покупатель-должник может пользоваться
    имуществом, вести хозяйственную деятельность, но не вправе продавать, дарить или менять заложенное имущество. Любое из этих действие противозаконно, если заемщик не уведомит о них
    залогодержателя, которым до погашения кредита выступает банк.


